<template>
  <div>
    <div class="conditions">
      <Row>
        <Col span="6">
        监测仓:
        <Select v-model="queryCondition.storeId" @on-change='changeStore(queryCondition.storeId)' style="width:12vw;">
          <Option v-for="item in sotres" :value="item.id" :key="item.id">{{ item.name }}
          </Option>
        </Select>
        </Col>
        <Col span="6" v-show="showBtn">
        区段:
        <Select v-model="queryCondition.sectionId" @on-change='changeSection(queryCondition.sectionId)'
                style="width:12vw;">
          <Option v-for="item in sections" :value="item.id" :key="item.id">{{ item.name }}</Option>
        </Select>
        </Col>
        <Col span="6" v-show="queryCondition.showDataMenu">
        监测数据类型:
        <Select v-model="queryCondition.objDataType" @on-change='changeDataType(queryCondition.objDataType)'
                style="width:12vw;">
          <Option v-for="item in dataTypeList" :value="item.val" :key="item.val">{{ item.key }}</Option>
        </Select>
        </Col>
      </Row>
    </div>
    <modal  v-bind="showModal"     @changeModalStatus="changeModalStatus"> </modal>
    <Tabs type="card" @on-click="changePane">
      <TabPane label="总览" name="total" icon="android-map">
        <diV class="gis" @mousemove ='updateXY'>
          <v_3DViewer :id="mapId" @onload="onload" >
          </v_3DViewer>
        </diV>
        <!--切换区段时显示的信息-->
        <div class="details" :style="{display:showSection}">
          <div class="tunnelsInfo">
            <div class="title">
              <h3>{{curName+curSotre.name+"概况"}}</h3>
            </div>
          </div>
          <div class="environmentalMonitoring">
            <div class="title">环境监测</div>
            <div class="inline-box" v-for=" item in curEnvData" :key="item.name">
              <div class="numerical">{{item.name}}</div>
              <div class="status"> {{item.value}}</div>
              <div
                :class="{'normal': item.status<50, 'abnormal': item.status >=50}"></div>
            </div>
          </div>
          <div class="theFireWarning">
            <div class="title">消防预警</div>
            <div class="inline-box">
              <div class="numerical">状态：</div>
              <div class="status"></div>
              <!--<div :class="{'normal': item.theFireWarningStatus=='正常'}"></div>-->
            </div>
          </div>
          <div class="monitoringSituation">
            <div class="title">监测状况</div>
            <div class="inline-box">
              <div class="numerical">状态：</div>
              <!--<div class="status">{{item.monitoringSituationStatus}}</div>-->
              <!--<div :class="{'normal': item.monitoringSituationStatus == '正常'}"></div>-->
            </div>
            <div class="inline-box">
              <div class="numerical">设备故障率：</div>
              <!--<div class="status">{{item.equipmentFailureRate}}%</div>-->
              <!--<div :class="{'normal': item.equipmentFailureRate<=2,'abnormal': item.equipmentFailureRate>2}"></div>-->
            </div>
          </div>
        </div>
        <div class="details" v-if="showSection==='none'">
          <div class="tunnelsInfo">
            <div class="tunnelProp">
              <h4>
                {{curName}}
              </h4>
              <div>
                <div style="width: 60%;float: left">
                  负责人：{{curTunnel.user}}
                </div>
                <div style="width: 40%;float: left">
                  监测仓个数: {{curTunnel.storeNum}}
                </div>
              </div>
              <div>
                <div style="width: 60%;float: left">
                  建设单位：{{curTunnel.constructionUnit}}
                </div>
                <div style="width: 40%;float: left">
                  区段数：{{curTunnel.sectionNum}}
                </div>
              </div>
              <div>
                <div>
                  运营单位：{{curTunnel.operationUnit}}
                </div>
              </div>
            </div>
          </div>
          <div style="padding:4% 10% 0 10%;height:53vh; overflow-y: auto;">
            <div style="float: left;width:50%" v-for="item in tunnelProps">
              <i-circle :percent="item.percent" :size="circleSize">
                <p>{{item.key}}</p></br>
                <p style="font-size:24px">{{item.val}}</p></br>
                <p>{{item.location}}</p>
              </i-circle>
            </div>
          </div>
        </div>
      </TabPane>
      <TabPane label="详情" name="detial" icon="ios-keypad">
        <div>
          <Page @on-change="changePage" :total="queryCondition.total"
                show-total show-elevator :page-size="queryCondition.pageSize" style="width: 70vw;float: left"></Page>
          <div style="position: relative;float: right;z-index: 1001" v-if="queryCondition.showSwitch">
            <span>总开关:</span>
            <i-switch size="large">
              <span slot="open">ON</span>
              <span slot="close">OFF</span>
            </i-switch>
          </div>
        </div>
        <Row :gutter="24">
          <Col span="6" v-for="item in Obj" :value="item.ObjName" :key="item.id">
          <SimulatedData v-bind="item" v-if="item.datatypeId==1"></SimulatedData>
          <showSwitchData v-bind="item" v-else @changeStatus="changeStatus"></showSwitchData>
          </Col>
        </Row>
        <div style="font-family: LED;">
        </div>
      </TabPane>
    </Tabs>
  </div>
</template>
<script>
  import  Modal from '../../../../components/Common/ShowMapDataModal.vue'
  import v_3DViewer from '../../../../components/Common/3DViewer'
  import SimulatedData from '../../../../components/UM/MAM/ShowSimulatedData'
  import showSwitchData from '../../../../components/UM/MAM/showSwitchData'
  import axios from 'axios'
  //灯
  import openLight from '../../../../assets/UM/openLight.png'
  import closeLight from "../../../../assets/UM/closeLight.png"
  //水泵
  import pumpstart from "../../../../assets/UM/pumpstart.gif"
  import pumpstop from "../../../../assets/UM/pumpstop.png"
  //风机
  import fanstart from "../../../../assets/UM/fanstart.gif"
  import fanstop from "../../../../assets/UM/fanstop.png"
  import {setViewAngle} from "../../../../scripts/commonFun.js"
  import {URL_CONFIG} from "../../../../../static/3DMap/js/3DMapConfig";
  export default {
    data() {
      return {
        screenWidth: document.body.clientWidth,
        Obj: [],
        scene: null,
        showModal: {
          show:false,
          top:0,
          left:0,
          data:[{key:"氧气浓度",val:"12.45%"},{key:"硫化氢",val:"45%"},{key:"一氧化碳",val:"34.2%"},{key:"二氧化碳",val:"17.9%"},{key:"湿度",val:"30%"}]
        },
        mapId: "tunnnelMap",
        curImgUrl: "",
        queryCondition: {
          tunnelId: null,      //监测仓ID
          storeId: null,        //监测区段ID
          sectionId: null,
          objDataType: null,    //监测数据类型
          showDataMenu: false,
          pageNum: 1,
          pageSize: 12,
          total: 100,
          IsDetial: false,
          showSwitch: false,
        },
        circleSize: 120,
        dataTypeList: [],
        tunnelId: 0,
        curName: "",
        showSection: "none",
        showBtn: false,
        btnStasus: true,
        tunnels: [],
        sotres: [],
        tunnelProps: [],//管廊统计数据
        tunnelPropsMax: [],//监测数据对应最大值
        sections: [],//管廊对应区段数据
        curTunnel: {
          user: "jisin",
          storeNum: 3,
          constructionUnit: "中建一局",
          operationUnit: '波汇科技',
          sectionNum: 10,
        }, //当前管廊数据
        curSotre: {
          id: '',
          name: '',
        },//当前监测仓数据
        curEnvData: [], //环境监测数据
        timer: null,
      }
    },
    watch: {
      '$route': function () {
        // $route发生变化时再次赋值
        this.tunnelId = this.$route.params.id;
        this.queryCondition.tunnelId = this.tunnelId;
        this.sotres = [];
        this.fentchData();
        this.showSection = "none";
        this.btnStasus = true;
        this.showBtn = false;
        this.setCircleSize();
      },
    },
    components: {v_3DViewer, SimulatedData, showSwitchData,Modal},
    mounted() {
      this.fentchData();
      this.setCircleSize();
      this.getDataType();
       // this.addARrea();
    },
    methods: {
      changeModalStatus(status){
        this.showModal.show=status;
      },
      //获取鼠标点击位置
      updateXY:function(e){
        e=e||window.event;
        this.showModal.left=e.offsetX+"px";
        this.showModal.top=e.offsetY+"px";
      },
      //加载超图模型
      onload(parent) {
        let _this=this;
        let Cesium = parent.Cesium;

        // 初始化viewer部件
        var viewer = new Cesium.Viewer(this.mapId, {
          navigation: false ,//关闭导航控件
          infoBox:false
        });
        var scene = viewer.scene,
          widget = viewer.cesiumWidget,
          imageryLayers = viewer.imageryLayers,
          imagery_mec;
        this.scene = scene;
        var provider_mec = new Cesium.SuperMapImageryProvider({
          url: URL_CONFIG.IMG_MAP //墨卡托投影地图服务
        });
        imagery_mec = imageryLayers.addImageryProvider(provider_mec);

        try {
          //打开所发布三维服务下的所有图层
          var promise = scene.open(URL_CONFIG.BIM_SCP);

          promise.then(function(layer) {
            // 将东西设置成不可选择
            layer.forEach(element => {
              // 结构性框架的要查数据，非结构性框架的不用管了，也不能选择
              if (element.name.indexOf("结构框架") < 0) {
                element.selectEnabled = false;
              } else {
                // 设置查找参数
                element.setQueryParameter({
                  url: URL_CONFIG.BIM_DATA,
                  dataSourceName: "tunnel",
                  dataSetName: "结构框架",
                  keyWord: "SmID"
                });
              }
            });
          });
          //注册鼠标点击事件
          viewer.pickEvent.addEventListener(function(feature){
            _this.showModal.show=true;

          // Cesium.defaultValue(feature.NAME,'');
          });

          // lay是所有的数据集
          Cesium.when(
            promise,
            function(layer) {
              //设置相机位置、视角，便于观察场景
              setViewAngle(scene, Cesium, _this.camera);
              viewer.pickEvent.addEventListener(function(feature) {
              });
            },
            function(e) {
              if (widget._showRenderLoopErrors) {
                var title =
                  "加载SCP失败，请检查网络连接状态或者url地址是否正确？";
                widget.showErrorPanel(title, undefined, e);
              }
            }
          );
        } catch (e) {
          if (widget._showRenderLoopErrors) {
            var title = "渲染时发生错误，已停止渲染。";
            widget.showErrorPanel(title, undefined, e);
          }
        }
      },
      //变更模型视角
      changeArea(area) {
        let _this = this;
        try {
          let location = area.split('/');
          _this.scene.camera.flyTo({
            destination: new Cesium.Cartesian3.fromDegrees(parseFloat(location[0]), parseFloat(location[1]), parseFloat(location[2])),
            orientation: {
              heading: parseFloat(location[3]),
              pitch: parseFloat(location[4]),
              roll: parseFloat(location[5])
            }
          })
        } catch (e) {
        }

      },
      //添加监测区段的视角位置
      addARrea() {
        let _this = this;
        _this.axios.post('/tunnels/stores',
          {
            "name": "区域-1",
            "tunnelId":2,
            "camera": "116.4452364361197/39.91946138913281/1.2353791933507945/4.798691472818731/-0.142284992117377/6.281842456965784",
            "storeTypeId":1,
            "crtTime": +new Date(),
          }
        ).then(result => console.log(result))
      },
      //切换路由
      goToMoudle(path) {
        this.$router.push(path);
      },
      //获取数据类型数据
      getDataType() {
        this.axios.get("/objecttype-enums").then(result => {
          let {code, data} = result.data;
          if (code == "200") {
            this.dataTypeList = data;
            this.queryCondition.objDataType = data[0].val;
          }
        })
      },
      //设置统计图表大小
      setCircleSize() {
        if (window.innerWidth < 1400) {
          this.circleSize = 100;
          if (this.tunnelProps.length > 6) {
            this.circleSize = 95
          }
        }
        else if (window.innerWidth > 1400 && window.innerWidth < 1800) {
          this.circleSize = 120;
          if (this.tunnelProps.length > 6) {
            this.circleSize = 105
          }
        }
        else if (window.innerWidth > 1800) {
          this.circleSize = 140;
          if (this.tunnelProps.length > 6) {
            this.circleSize = 115
          }
        }
      },
      //变更监测仓
      changeStore() {
        if (this.queryCondition.storeId) {
          this.showSection = "";
          this.btnStasus = false;
          this.showBtn = true;
          //获取区段列表
          this.axios.get("tunnels/stores/" + this.queryCondition.storeId + "/sections").then(response => {
            let {code, data} = response.data;
            if (code == 200) {
              this.sections = data.sort((a, b) => a.id - b.id);
              this.curSotre.id = this.sections[0].id;
              this.curSotre.name = this.sections[0].name;
              this.getSectionsMonitorData();
            }
          })
          //获取位置信息
          let curView =this.sotres.filter(a=>a.id==this.queryCondition.storeId)[0].camera;
          this.changeArea(curView);
          if (this.IsDetial) {
            this.getObjDetialData();
          }
        }
      },
      //变更区段
      changeSection() {
        if (this.queryCondition.sectionId) {
          this.curSotre.id = this.queryCondition.sectionId;
          this.curSotre.name = this.sections.filter(a => a.id == this.curSotre.id)[0].name;
          this.getSectionsMonitorData();
          if (this.IsDetial) {
            this.getObjDetialData();
          }
        }
      },
      //变更数据类型
      changeDataType() {
        if (this.queryCondition.objDataType) {
          this.getObjDetialData();
        }
      },
      //获取数据
      fentchData() {
        this.tunnelId = this.$route.params.id;
        this.queryCondition.tunnelId = this.tunnelId;
        //获取管廊列表
        this.axios.get("/tunnels").then(response => {
          let {code, data} = response.data;
          if (code == 200) {
            this.tunnels = data;
            this.tunnels.forEach(a => {
              if (a.id == this.tunnelId) {
                this.curName = a.name;
              }
            });
          }
        }),

          //获取管廊详细信息
          this.axios.get("tunnels/" + this.tunnelId).then(response => {
            let {code, data} = response.data;
            if (code == 200) {
              this.curTunnel.user = data.responsibility.name;
              this.curTunnel.constructionUnit = data.construct.name;
              this.curTunnel.operationUnit = data.operation.name;
            }
          })

        //获取管仓列表     url：tunnels/{id}/stores
        this.axios.get("tunnels/" + this.tunnelId + "/stores").then(response => {
          let {code, data} = response.data;
          if (code == 200) {
            this.sotres = data;
          }
        })
        axios.all([
          this.axios.get("tunnels/" + this.tunnelId + "/ai/max-cv/"),
          this.axios.get("sections/ai/standard-cv")
        ]).then(axios.spread((prop, maxVal) => {
          this.tunnelProps = [];
          this.tunnelPropsMax = [];
          this.tunnelPropsMax = maxVal.data.data;
          prop.data.data.forEach(a => {
            let temp = {};
            temp.location = a.location;
            temp.key = a.key;
            temp.val = new Number(a.val.toFixed(2));
            temp.percent = temp.val / (this.tunnelPropsMax.filter(a => a.key == temp.key)[0].val) * 100;
            this.tunnelProps.push(temp);
          });
        }))
          .catch(error => {
          })

        //获取指定管廊下共有多少管仓    url:tunnels/{id}/stores/count
        this.axios.get("tunnels/" + this.tunnelId + "/stores/count").then(response => {
          let {code, data} = response.data;
          if (code == 200) {
            this.curTunnel.storeNum = data.val;
          }
        })
        //获取区段数
        this.axios.get("tunnels/" + this.tunnelId + "/sections/count").then(response => {
          let {code, data} = response.data;
          if (code == 200) {
            this.curTunnel.sectionNum = data.val;
          }
        })
      },
      //点击切换面板事件
      changePane(name) {
        if (name == "detial") {
          this.IsDetial = true;
          this.queryCondition.showDataMenu = true;
          this.getObjDetialData();
        }
        else {
          this.IsDetial = false;
          this.queryCondition.showDataMenu = false;
        }
      },
      //获取监测仓具体区段的环境监测数据
      getSectionsMonitorData() {
        this.axios.get("tunnels/stores/sections/" + this.curSotre.id + "/ai/cv").then(response => {
          let {code, data} = response.data;
          if (code == 200) {
            this.curEnvData = [];
            data.forEach(a => {
              let temp = {};
              temp.name = a.objTypeName;
              temp.value = new Number(a.cv).toFixed(2);
              temp.status = new Number(temp.value) / (this.tunnelPropsMax.filter(a => a.key == temp.name)[0].val) * 100
              this.curEnvData.push(temp);
            })
          }
        })
      },
      //切换开关量控制
      changeStatus(id, status) {
        this.switchImg(this.queryCondition.objDataType, status);
        this.Obj.filter(a => a.id == id)[0].imgUrl = this.curImgUrl;
      },
      //获取详情面板的数据
      getObjDetialData() {
        let _this = this;
        var Params = {
          tunnelId: _this.queryCondition.tunnelId,
          storeId: _this.queryCondition.storeId,
          objtypeId: _this.queryCondition.objDataType,
          sectionId: _this.queryCondition.sectionId,
          pageNum: _this.queryCondition.pageNum,
          pageSize: _this.queryCondition.pageSize,
        };
        _this.axios.post('measobjs/datagrid', Params).then(result => {
          let {code, data} = result.data;
          let ids = "";
          let datatypeId = null;
          if (code == "200") {
            _this.Obj = [];
            data.list.forEach(a => {
              let temp = {};
              temp.ObjName = a.name;
              temp.id = a.id;
              temp.ObjVal = "";
              temp.datatypeId = a.datatypeId;
              ids += a.id + ",";
              datatypeId = a.datatypeId;
              temp.imgUrl = "";
              temp.objtypeName = _this.curName + _this.curSotre.name + "---" + a.objtypeName;
              _this.Obj.push(temp);
            })
            _this.queryCondition.total = data.total;
            _this.queryCondition.pageNum = data.pageNum;
            ids = ids.slice(0, ids.length - 1);
            if (data.list != null && data.list.length > 0) {
              _this.axios.get('measobjs/' + ids + '/' + datatypeId + '/cache-cv').then(result => {
                let {code, data} = result.data;
                if (code == "200") {
                  _this.Obj.forEach(a => {
                    data.filter(b => {
                      if (a.id == b.id) {
                        if (datatypeId == 1) {
                          a.ObjVal = b.cv.toFixed(2);
                          this.queryCondition.showSwitch = false;
                          _this.switchImg(_this.queryCondition.objDataType, a.ObjVal);
                          a.imgUrl = this.curImgUrl;
                        }
                        else {
                          a.ObjVal = b.cv;
                          this.queryCondition.showSwitch = true;
                          _this.switchImg(_this.queryCondition.objDataType, a.ObjVal);
                          a.imgUrl = this.curImgUrl;
                        }
                      }
                    })
                  })
                }
              })
            }
          }
        });
      },
      //切换页面
      changePage(index) {
        let _this = this;
        _this.queryCondition.pageNum = index;
        _this.getObjDetialData();
      },
      //切换页码数
      handlePageSize(value) {
        this.queryCondition.pageSize = value;
        this.getObjDetialData();
      },
      //根据状态切换图片
      switchImg(datatype, status) {
        if (datatype != null) {
          switch (datatype) {
            case 1: //温度
              if (status) {
                this.curImgUrl = "";
                break;
              }
              else {
                this.curImgUrl = "";
                break;
              }
            case 2: //湿度
              this.curImgUrl = "";
              break;
            case 3: //氧气
              this.curImgUrl = "";
              break;
            case 4: //硫化氢
              this.curImgUrl = "";
              break;
            case 5: //甲烷
              this.curImgUrl = "";
              break;
            case 6: //一氧化碳
              this.curImgUrl = "";
              break;
            case 7: //视频
              this.curImgUrl = "";
              break;
            case 8: //分布式温度
              this.curImgUrl = "";
              break;
            case 9: //机器人
              this.curImgUrl = "";
              break;
            case 10: //风机
              if (status) {
                this.curImgUrl = fanstart;
                break;
              }
              else {
                this.curImgUrl = fanstop;
                break;
              }
              this.curImgUrl = "";
              break;
            case 11: //灯
              if (status) {
                this.curImgUrl = openLight;
                break;
              }
              else {
                this.curImgUrl = closeLight;
                break;
              }
            default:
              break;
          }
        }

      },
    },
  }
</script>
<style scoped>
  .conditions {
    margin: 10px;
  }
  .ivu-modal-wrap  >>>  .ivu-modal {
    left: 500px;
    top: 500px;
  }
  @font-face {
    font-family: 'LED';
    src: url('/static/VM/css/DS-DIGIT.TTF') format('truetype');
    font-weight: normal;
    font-style: normal;
  }

  .gis, .details {
    display: inline-block;
    border: 1px solid #b3b0b0;
    height: 76vh;
    vertical-align: top;
  }

  .gis {
    width: 59vw;
  }

  .details {
    border: 1px solid #b3b0b0;
    overflow-y: auto;
    width: 24.3vw;
  }

  .Section #pageBox {
    border: 1px solid #b3b0b0;
    bottom: 0px;
    width: 83.5vw;
    left: 0px;
    text-align: center;
    overflow-x: hidden;
  }

  .showSection {
    display: inline-block;
    line-height: 35px;
  }

  .showSection {
    overflow: hidden;
    padding-left: 20%;
  }

  .showSection ul li {
    display: inline-block;
    border-right: 1px solid #b3b0b0;
    line-height: 35px;
    text-align: center;
  }

  .showSection ul li {
    width: 100px;
  }

  .tunnelsInfo, .environmentalMonitoring, .theFireWarning, .monitoringSituation {
    margin: 0.5vw;
    border: 1px solid #b3b0b0;
    border-radius: 8px;
    padding-left: 10%;
    box-shadow: 5px 6px 4px rgba(0, 0, 0, .2);
    border-color: #eee;
  }

  .active {
    background: #2D8CF0;
    color: #ffffff;
  }

  .tunnelsInfo div, .environmentalMonitoring div, .theFireWarning div, .monitoringSituation div {
    line-height: 5vh;
    font-size: 14px;
  }

  .title {
    font-size: 20px !important;
    text-align: center;
    font-weight: 700;
    margin-top: 5px;
  }

  .inline-box div {
    display: inline-block;
  }

  .normal {
    width: 15px !important;
    height: 15px !important;
    border-radius: 100%;
    background: green;
  }

  .abnormal {
    width: 15px !important;
    height: 15px !important;
    border-radius: 100%;
    background: red;
  }

  .numerical {
    width: 60%;
  }

  .status {
    width: 20%;
  }
</style>



