<template>
  <div>
    <Form ref="addCustomerInfo" :model="addCustomerInfo"  :rules="ruleValidate" :label-width="80">
        <h2 v-show="pageType!=pageTypes.Edit">添加客户信息</h2>
        <h2 v-show="pageType==pageTypes.Edit">修改客户信息</h2>
        <FormItem label="所属公司" prop="companyId">
            <Select v-model="addCustomerInfo.companyId">
                <Option v-for="item in company" :value="item.id.toString()" :key="item.id">{{ item.name }}</Option>
            </Select>
        </FormItem>
        <FormItem label="联系人" prop="contact">
            <Input v-model="addCustomerInfo.contact" placeholder="请输入联系人姓名"></Input>
        </FormItem>
        <FormItem label="联系方式" prop="tel">
            <Input  v-model="addCustomerInfo.tel" placeholder="请输入联系方式" @on-blur="validateTel(addCustomerInfo.tel)"></Input>
            <div class="ivu-form-item-error-tip" v-show="validateTelRules==false">请填写正确的手机号</div>
        </FormItem>
    </Form>
    <div style="text-align: center">
      <Button type="primary" @click="submitAddCustomerInfo('addCustomerInfo')" v-show="pageType!=pageTypes.Edit" :disabled="isDisable">提交</Button>
      <Button type="primary" @click="submitEditCustomerInfo('addCustomerInfo')" v-show="pageType==pageTypes.Edit" :disabled="isDisable">更新</Button>
      <Button type="ghost"  @click="handleReset('addCustomerInfo')" style="margin-left: 8px">取消</Button>
    </div>

  </div>
</template>
<script>
import axios from "axios";
import types from '../../../../../static/Enum.json'
export default {
    data(){
        return{
            pageType: 2,
            pageTypes: types.pageType,
            addCustomerInfo:{
                id: 1,
                companyId: null,
                contact: null,
                tel: null
            },
            company:[],
            editCustomerInfo:[],
            ruleValidate: {
                companyId: [
                    { required: true, message: '请选择所属公司', trigger: 'change' }
                ],
                contact: [
                    { required: true, message: '请填写联系人', trigger: 'blur' }
                ],
                tel: [
                    { required: true, message: '请填写联系方式', trigger: 'blur' }
                ]
            },
          validateTelRules: true,
          isDisable:false
        }
    },
    mounted(){
        axios.get('/companies').then(response=>{
            let{ code,data } = response.data
            if(code==200){
                this.company=data
            }
        });
        this.addCustomerInfo.id = this.$route.params.id;
        this.pageType = this.$route.params.type
        this.getParams()
    },
    methods:{
      validateTel(tel){
        if(tel==null||tel==undefined||tel==""){
            this.validateTelRules = true;
        }else{
            if(!(/^1(3|4|5|7|8)\d{9}$/).test(tel)){
                this.validateTelRules = false
                return this.validateTelRules
            }else{
                this.validateTelRules = true
                return this.validateTelRules
            }
        }
      },
        submitAddCustomerInfo(name){
            this.isDisable = true
            setTimeout(()=>{
                this.isDisable = false
                this.$refs[name].validate((valid)=>{
                  if(valid){
                    if(this.validateTelRules==true){
                      this.axios.post("/customers", this.addCustomerInfo).then(response => {
                          console.log(this.addCustomerInfo)
                      })
                        .catch(function(error) {
                          console.log(error);
                        });
                      this.$router.push("/UM/tunnelCustomer/list");
                    }
                  }else{
                    this.$Message.error("请填写正确的客户信息")
                  }
                })
            },2000)

        },
        getParams(){
            if(this.pageType==this.pageTypes.Edit){
                axios.get('/customers/'+this.addCustomerInfo.id).then(response=>{
                    this.addCustomerInfo = response.data.data
                    this.addCustomerInfo.companyId = response.data.data.company.id.toString()
                    this.addCustomerInfo.tel = response.data.data.tel
                    this.validateTel(this.addCustomerInfo.tel)
                })
            }
        },
        submitEditCustomerInfo(name){
            this.isDisable = true
            setTimeout(()=>{
                this.$refs[name].validate((valid)=>{
                if(valid){
                  if(this.validateTelRules==true){
                    axios.put('customers',this.addCustomerInfo).then(response=>{
                    })
                      .catch(function(error){
                        console.log(error)
                      })
                    this.$router.push("/UM/tunnelCustomer/list");
                  }
                }else{
                  this.$Message.error("请填写正确的客户信息")
                }
              })
            },2000)
        },
      handleReset(name){
          this.$refs[name].resetFields()
      },

    }
}
</script>
<style scoped>
.ivu-form.ivu-form-label-right{
    width: 500px;
    margin: 20px auto;
}
h2{
    text-align: center;
    margin-bottom: 20px;
}
</style>



