          <Tabs value="name1">
            <TabPane label="间隔模式" name="name1">
              <div>
                开始日期：
                <input  v-model="intervalMode.startDay" type="number" min="1" max="31" value="1">
                结束日期：<input  v-model="intervalMode.endDay" type="number" min="1" max="31" value="30">
                间隔天数：<input  v-model="intervalMode.interval" type="number" min="1" max="31" value="1">
                <Button type="default" @click="getDay">确定</Button>
                <Button type="default" @click="defaultBtn">重置</Button>
              </div>
            </TabPane>
            <TabPane label="星期模式" name="name2">
              <Checkbox v-for="(item, index) in weekDay" v-model="item.value" :value='item.value' :key='index' @on-change="getWeek">{{item.name}}</Checkbox>
            </TabPane>
            <TabPane label="自定义" name="name3">
              请选择您需要的时间
            </TabPane>
          </Tabs>
          <calender ref="calender" v-on:childByValue="getActiveText"></calender>
        </FormItem>
        <FormItem style="text-align: center">
            <Button type="primary" @click="submitPlan">提交</Button>
            <Button type="ghost"  style="margin-left: 8px">取消</Button>
        </FormItem>
      </Form>  
    </div>
</template>  
