<script>
import axios from "axios";
import calender from "../../../Common/calender.vue";
export default {
  name: "plans",
  components: {
    calender
  },
  data() {
    return {
      uploadPlan:{
        id: "MA1000000",
        name: "",
        groupId: null,
        inspectTime: "",
        tasks:[],
        approverId: 1,
        tunnelId: null,
        requestStaffId: 1,
        
      },
      groups: [{
        "id":1,
        "name":"group1",
        "leaderId":1,
        "leaderName":"l1"
      }],
      selectedMode:"",
      tunnels: [{
        "id":1,
        "name":"管廊1",
      }],
      weekDay:[
        {
          name:"星期日",
          value: false
        },
        {
          name:"星期一",
          value: false
        },
        {
          name:"星期二",
          value: false
        },
        {
          name:"星期三",
          value: false
        },
        {
          name:"星期四",
          value: false
        },
        {
          name:"星期五",
          value: false
        },
        {
          name:"星期六",
          value: false
        }
      ],
      intervalMode:{
        startDay: 1,
        endDay: 31,
        interval: 3
      }
    };
  },
  watch: {
    '$route': function () {
      //2. $route发生变化时再次赋值planId
      this.uploadPlan.tunnelId = this.$route.params.id;
      this.tunnels.forEach(a => {
        if (a.id == this.tunnelId) {
          this.computedPageSize();
          this.conditionChange();
        }
      });
    }
  },
  mounted() {
  //   从数据库读取所属管廊select的option选项
    axios.get("/tunnels ").then(response => {
      let { code, data } = response.data;
      if (code == 200) {
        this.tunnels = data;
      }
    });
  //     从数据库读取责任班组select的option选项
    axios.get("/inspection-groups").then(response => {
      let { code, data } = response.data;
      if (code == 200) {
        this.groups = data;
      }
    });
  },
  methods: {

    submitPlan() {
      //提交数据
      this.axios.post("/inspection-plans", this.uploadPlan).then(response => {
        this.$router.push("/UM/patrol/query/"+this.tunnelId);
        console.log(this.uploadPlan)
      })
      .catch(function(error) {
        console.log(error);
      });
    },
    getDay() {
      this.$refs.calender.cancelActive();
      this.$refs.calender.interVal(this.intervalMode.startDay, this.intervalMode.endDay, this.intervalMode.interval);
      this.$refs.calender.outputHeighLight();
    },
    defaultBtn() {
      this.$refs.calender.cancelActive();
      this.$refs.calender.outputHeighLight();
    },
    getWeek(){
      this.$refs.calender.cancelActive();
      var week = [];
      for (let index = 0; index < this.weekDay.length; index++) {
        if (this.weekDay[index].value) week.push(index);
      }
      this.$refs.calender.chooseWeek(week);
      this.$refs.calender.outputHeighLight();
    },
    getActiveText(childValue){
      this.uploadPlan.tasks=[];
      for(var i=0;i<childValue.length;i++){
          this.uploadPlan.tasks.push({taskTime:childValue[i]})
      }
    }
  }
}
</script>

<style  scoped>
/* .planBtn {
