<template>
  <div>
    <ModulePage v-bind="patrolScheme"></ModulePage>
  </div>
</template>

<script>
import ModulePage from '../../../../components/Common/ModulePage'
export default {
  name: "UM_main",
  components:{ModulePage},
  data() {
    return {
        patrolScheme:{
          moduleName:"巡检计划",
          leftTree:[]
        },
        treeNodeJumpUrl:"/UM/patrol/homePage",
    };
  },
  created(){
    this.patrolScheme.leftTree = [];
    this.axios.get("/tunnels").then(result => {
      let {code, data} = result.data;
      var _this=this;
      if (code == 200) {
        data.forEach(a=>{
          let temp={};
          temp.id=a.id;
          temp.name=a.name;
          temp.url="";
          var child=[{id:a.id+"1",name: "查询", url: "/UM/patrol/query/"+a.id},
                      {id:a.id+"2",name: '制定', url: '/UM/patrol/add'}];
                      temp.childNode= child;
          _this.patrolScheme.leftTree.push(temp);
        })
      }
    });
  },
};
</script>


