<template>
  <div>
    <ModulePage v-bind="equipemnt"></ModulePage>
  </div>
</template>

<script>
import ModulePage from '../../../../components/Common/ModulePage'
export default {
  name: "UM_main",
  components: { ModulePage },
  data() {
    return {
      equipemnt: {
        moduleName: "设备管理",
        leftTree: [],
      },
      treeNodeJumpUrl: "/UM/equipment/homePage",
    };
  },
  created() {
    this.axios.get("/tunnels").then(result => {
      let { code, data } = result.data;
      this.equipemnt.leftTree = [];
      var _this = this;
      if (code == 200) {
        data.forEach(a=>{
          let temp={};
          temp.id=a.id;
          temp.name=a.name;
          temp.url=_this.treeNodeJumpUrl;
          var child=[{id:a.id+"1",name: '查询', url: '/UM/equipment/query/'+a.id},
                      {id:a.id+"2",name: '制定', url: '/UM/equipment/add'}];
                      temp.childNode= child;
          _this.equipemnt.leftTree.push(temp);
        })
      }
    });
  },
};
</script>


