<template>
    <div style="padding: 10px">
        <Row style="margin-bottom: 20px;">
            <Col span="6">
                监测仓:
                <Select v-model="conditions.tunnelId" style="width:60%">
                    <Option value=null key="0">所有</Option>
                    <Option v-for="item in tunnels" :value="item.id" :key="item.id">{{ item.name }}</Option>
                </Select>
            </Col>
        </Row>
        <Row>
            <Col span="14">
                <div class="imgBox">
                    <!-- <video></video> -->
                    <img :src="robotTop">
                </div>
            </Col>
            <Col span="10">
                <!-- <div class=""> -->
                    <div  align="right">
                        <Input style="width: 60%;">
                            <Button slot="append" icon="ios-search"></Button>
                        </Input>
                    </div>
                    <div class="videoBox">
                        <div class="video">
                            <!-- <video></video> -->
                            <img :src="robotTop">
                        </div>
                        <div class="video">
                            <!-- <video></video> -->
                            <img :src="robotTop">
                        </div>
                        <div class="video">
                            <!-- <video></video> -->
                            <img :src="robotTop">
                        </div>
                        <div class="video">
                            <!-- <video></video> -->
                            <img :src="robotTop">
                        </div>
                        <div class="video">
                            <!-- <video></video> -->
                            <img :src="robotTop">
                        </div>
                        <div class="video">
                            <!-- <video></video> -->
                            <img :src="robotTop">
                        </div>
                        <div class="video">
                            <!-- <video></video> -->
                            <img :src="robotTop">
                        </div>
                        <div class="video">
                            <!-- <video></video> -->
                            <img :src="robotTop">
                        </div>
                        <div class="video">
                            <!-- <video></video> -->
                            <img :src="robotTop">
                        </div>
                    </div>
                <!-- </div> -->
            </Col>
        </Row>
    </div>
</template>
<script>
import axios from "axios";
import robotTop from '../../../../assets/UM/robotTop.png'
export default {
    data(){
        return{
            tunnels:[],
            conditions:{
                tunnelId: null
            }
        }
    },
    mounted(){
        // 从数据库读取select的option选项
        axios.get("/tunnels ").then(response => {
            let { code, data } = response.data;
            if (code == 200) {
                this.tunnels = data;
            }
        });
    }
}
</script>
<style scoped>
    .imgBox{
        width: 100%;
        height: 70vh;
        padding: 0 40px 0px 0px;
    }
    .videoBox{
        height: 70vh;
        border: 1px solid #fff;
        overflow-y: auto;
        margin-top: 10px;
    }
    .imgBox img,.videoBox .video img{
        width: 100%;
        height: 100%;
    }
    .video{
        width: 49%;
        display: inline-block;
        padding: 10px 0px 5px 15px;
    }
</style>



