<template>
<div id="calendar">
    <!-- 年份 月份 -->
    <div class="month">
        <ul>
            <!--点击会触发pickpre函数，重新刷新当前日期 @click(vue v-on:click缩写) -->
            <li class="arrow" @click="pickPre(currentYear,currentMonth)">❮</li>
            <li class="year-month" @click="pickYear(currentYear,currentMonth)">
                <span class="choose-year">{{ currentYear }}</span>
                <span class="choose-month">{{ currentMonth }}月</span>
            </li>
            <li class="arrow" @click="pickNext(currentYear,currentMonth)">❯</li>
        </ul>
    </div>
    <!-- 星期 -->
    <ul class="weekdays">
        <li>一</li>
        <li>二</li>
        <li>三</li>
        <li>四</li>
        <li>五</li>
        <li style="color:red">六</li>
        <li style="color:red">日</li>
    </ul>
    <!-- 日期 -->
    <ul class="days">
        <!-- 核心 v-for循环 每一次循环用<li>标签创建一天 -->
        <li v-for="(dayobject,index) in days" :key="index">
            <!--本月-->
            <!--如果不是本月  改变类名加灰色-->
            <span v-if="dayobject.day.getMonth()+1 != currentMonth" class="other-month">{{ dayobject.day.getDate() }}</span>
            <!--如果是本月  还需要判断是不是这一天-->
            <span v-else>
          <!--今天  同年同月同日-->
                <span ref="dayLi" @click="selectDay($event)"  v-if="dayobject.day.getFullYear() == new Date().getFullYear() && dayobject.day.getMonth() == new Date().getMonth() && dayobject.day.getDate() == new Date().getDate()" class="curDay">{{ dayobject.day.getDate() }}</span>
            <span ref="dayLi" @click="selectDay($event)" v-else>{{ dayobject.day.getDate() }}</span>
            </span>



        </li>
    </ul>
</div>
</template>
<script>
export default {
    data() {
        return {
            currentDay: 1,
            currentMonth: 1,
            currentYear: 1970,
            currentWeek: 1,
            // 日曆顯示日期數
            showDayNum: 42,
            days: [],
            class: 'active',
            arrWeek: [],
            selectedWeek: []
        }
    },
    // props:[
    //     "week"
    // ],
    created: function() { //在vue初始化时调用
        this.initData(null);
    },
    methods: {
        initData: function(cur) {
            var leftcount = 0; //存放剩余数量
            var date;


            if (cur) {
                date = new Date(cur);
            } else {
                var now = new Date();
                var d = new Date(this.formatDate(now.getFullYear(), now.getMonth(), 1));
                d.setDate(this.showDayNum);
                date = new Date(this.formatDate(d.getFullYear(), d.getMonth() + 1, 1));
            }
            this.currentDay = date.getDate();
            this.currentYear = date.getFullYear();
            this.currentMonth = date.getMonth() + 1;

            this.currentWeek = date.getDay(); // 1...6,0
            if (this.currentWeek == 0) {
                this.currentWeek = 7;
            }
            var str = this.formatDate(this.currentYear, this.currentMonth, this.currentDay);
            this.days.length = 0;
            // 今天是周日，放在第一行第7个位置，前面6个
            //初始化本周
            for (var i = this.currentWeek - 1; i >= 0; i--) {
                var d = new Date(str);
                d.setDate(d.getDate() - i);
                var dayobject = {}; //用一个对象包装Date对象  以便为以后预定功能添加属性
                dayobject.day = d;
                this.days.push(dayobject); //将日期放入data 中的days数组 供页面渲染使用


            }
            //其他周
            for (var i = 1; i <= this.showDayNum - this.currentWeek; i++) {
                var d = new Date(str);
                d.setDate(d.getDate() + i);
                var dayobject = {};
                dayobject.day = d;
                this.days.push(dayobject);
            }

        },
        pickPre: function(year, month) {

            // setDate(0); 上月最后一天
            // setDate(-1); 上月倒数第二天
            // setDate(dx) 参数dx为 上月最后一天的前后dx天
            var d = new Date(this.formatDate(year, month, 1));
            d.setDate(0);
            this.initData(this.formatDate(d.getFullYear(), d.getMonth() + 1, 1));
        },
        pickNext: function(year, month) {
            var d = new Date(this.formatDate(year, month, 1));
            d.setDate(this.showDayNum);
            this.initData(this.formatDate(d.getFullYear(), d.getMonth() + 1, 1));
        },
        pickYear: function(year, month) {
            alert(year + "," + month);
        },

        // 返回 类似 2016-01-02 格式的字符串
        formatDate: function(year, month, day) {
            var y = year;
            var m = month;
            if (m < 10) m = "0" + m;
            var d = day;
            if (d < 10) d = "0" + d;
            return y + "-" + m + "-" + d
        },

        //取消所有的active
        cancelActive() {
            var arr = this.$refs.dayLi;
            for (var i = 0; i < arr.length; i++) {
                if (arr[i].getAttribute("class") == 'active') {
                    arr[i].setAttribute('class', '');
                }
            }
            this.selectedWeek = [];
        },

        //输出所有的高亮显示内容
        outputHeighLight() {
            var arr = this.$refs.dayLi;
            let activeText = [];
            for (var i = 0; i < arr.length; i++) {
                if (arr[i].getAttribute("class") == 'active') {
                    let HeighLightDay = new Date(this.currentYear, this.currentMonth - 1, Number(arr[i].innerText));
                    activeText.push(HeighLightDay);
                }
            }
            this.$emit('childByValue', activeText)
        },

        //显示当前点击对象
        selectDay(event) {
            if (event.currentTarget.getAttribute("class") == 'active') {
                event.currentTarget.setAttribute('class', '');
            } else {
                event.currentTarget.setAttribute('class', 'active');
            }
            this.outputHeighLight();
        },
        //间隔模式
        interVal(startNum, endNum, intervalNum) {
            var arr = this.$refs.dayLi;
            if (endNum > arr.length) endNum = arr.length;
            while (startNum <= endNum){
                arr[startNum - 1].setAttribute("class", "active");
                startNum = Number(startNum) + Number(intervalNum);
            }
        },

        //选择星期
        chooseWeek(arrWeek) {
            //循环所有的天数
            var arr = this.$refs.dayLi;
            for (var i = 0; i < arr.length; i++) {
                let t = new Date(this.currentYear, this.currentMonth - 1, i + 1);
                // 判断当前星期在不在列表中
                if (arrWeek.indexOf(t.getDay()) >= 0) arr[i].setAttribute("class", "active");
            }
        }

    }
}
</script>

<style scoped>
* {
    box-sizing: border-box;
}

ul {
    list-style-type: none;
}

body {
    font-family: Verdana, sans-serif;
    background: #e8f0f3;
}

#calendar {
    width: 400px;
    margin-top: 20px;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.1), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
}

.month {
    width: 100%;
    background: #00b8ec;
}

.month ul {
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: space-between;
}

.year-month {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

.year-month:hover {
    background: rgba(150, 2, 12, 0.1);
}

.choose-year {
    padding-left: 20px;
    padding-right: 20px;
}

.choose-month {
    text-align: center;
    font-size: 1.5rem;
}

.arrow {
    padding: 30px;
}

.arrow:hover {
    background: rgba(100, 2, 12, 0.1);
}

.month ul li {
    color: white;
    font-size: 20px;
    text-transform: uppercase;
    letter-spacing: 3px;
}

.weekdays {
    margin: 0;
    padding: 10px 0;
    background-color: #00b8ec;
    display: flex;
    flex-wrap: wrap;
    color: #ffffff;
    justify-content: space-around;
}

.weekdays li {
    display: inline-block;
    width: 13.6%;
    text-align: center;
}

.days {
    padding: 0;
    background: #ffffff;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.days li {
    list-style-type: none;
    display: inline-block;
    width: 57.1px;
    text-align: center;
    line-height: 57.1px;
    font-size: 1rem;
    color: #000;
}

.days li .curDay {
    padding: 6px 10px;
    border-radius: 50%;
    color: #00b8ec;
}

.days li .active {
    /* padding: 6px 10px; */
    border-radius: 50%;
    background: #00b8ec;
    color: #fff;
    display: block;
    height: 35px;
    width: 35px;
    line-height: 35px;
    margin-top: 11px;
    margin-left: 10px;
    margin-bottom: 11px;
}

.days li .other-month {
    padding: 5px;
    color: gainsboro;
}
</style>



