<template>
    <div>
        <div class="conditions">
            <Row>
                <Col span="6">
                    流程类型：
                    <Select style="width:60%" v-model="conditions.processType" @on-change="queryMyTask()">
                        <Option value=null>所有</Option>
                        <Option v-for="(item,index) in processType" :value="item.val" :key="index">{{item.key}}</Option>
                    </Select>
                </Col>
                <Col span="6">
                    <span class="conditionTitle">开始时间：</span>
                    <DatePicker type="datetime" placeholder="请选择开始时间" style="width: 60%" v-model="conditions.startTime" @on-change="queryMyTask()"></DatePicker>
                </Col>
                <Col span="6">
                    <span class="conditionTitle">结束时间：</span>
                    <DatePicker type="datetime" placeholder="请选择结束时间" style="width: 60%" v-model="conditions.endTime" @on-change="queryMyTask()"></DatePicker>
                </Col>
            </Row>
        </div>
        <Table stripe border :columns="columns1" :data="myTask"></Table>
    </div>    
</template>
<script>
import axios from "axios";
export default {
    data(){
        return{
            id: null,
            columns1:[
                {
                    type: 'index',
                    width: 60,
                    align: 'center'
                },
                {
                    title: '申请类型',
                    key: 'processTypeName',
                    // width: 100,
                    align: 'center'
                },
                {
                    title: '申请人',
                    key: "staff",
                    align: 'center',
                    render: (h,params) => {
                        return h("div", params.row.staff.name);
                    }
                },
                {
                    title: '申请时间',
                    key: 'crtTime',
                    align: 'center',
                    render: (h,params) => {
                        return h('div', new Date(params.row.crtTime).format('yyyy-MM-dd hh:mm:s'))
                    }
                },
                {
                    title: '操作',
                    align: 'center',
                    render: (h,params) => {
                        let url = ''
                        if(params.row.processType==2){
                            url = '/UM/myNews/examineApprove/'+params.row.processDefinitionId+'/'+params.row.processType
                        }else if(params.row.processType==1){
                            url = '/UM/myNews/examinePlans/'+params.row.id
                        }
                        return h('Button',{
                            props: {
                                type: "primary",
                                size: "small"
                            },
                            on: {
                                click: () => {
                                    this.goToMoudle({path: url})
                                }
                            }
                        },'查看申请')
                    }
                }
            ],
            myTask:[],
            processType:[],
            conditions:{
                processType: null,
                startTime: null,
                endTime: null
            },
            page:{
                pageNum: 1,
                pageSize: 10
            }
        }
    },
    computed:{
        params() {
            let param = {
                pageNum: this.page.pageNum,
                pageSize: this.page.pageSize,
                processType: this.conditions.processType,
                startTime: new Date(this.conditions.startTime).getTime(),
                endTime: new Date(this.conditions.endTime).getTime()
            };
            return Object.assign({}, param);
        }
    },
    mounted() {
        axios.get('processtype-enums').then(response=>{
            let{code,data} = response.data
            if(code==200){
                this.processType = data
            }
        })
        this.queryMyTask()
    },
    methods: {
        queryMyTask(){
            axios.post('users/activiti/task/datagrid',(this.params)).then(response=>{
                let { code, data } = response.data;
                if (code == 200) {
                    this.myTask = data.list
                }
            })

        },
        goToMoudle: function (path) {
            this.$router.push(path);
        },
    }
}
</script>
<style scoped>
    .conditions{
        margin-bottom: 20px;
    }
</style>



