// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import 'es6-promise/auto'
import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'
import iView from 'iview'
import axios from 'axios'
import VueAxios from 'vue-axios'
import 'iview/dist/styles/iview.css'
import routes from "./router";
import Vuex from 'vuex'
import store from './store'
import echarts from 'echarts'
import 'animate.css/animate.min.css'

// import 'element-ui/lib/theme-chalk/index.css'
const ApiUrl=require('../static/serverconfig').ApiUrl;
const RouterBase=require('../static/serverconfig').RouterBase;
Vue.prototype.$echarts = echarts

Vue.use(VueRouter)
Vue.use(Vuex)
Vue.use(iView);
axios.get("/static/serverconfig.json").then((result)=>{
  localStorage.setItem('ApiUrl',result.data.ApiUrl);
}).catch((error)=>{console.log(error)});
var axios_instance = axios.create({
  headers: {
    'Content-Type': 'application/json;charset=utf-8',
  },
  withCredentials: true
})
Vue.use(VueAxios, axios_instance)
Vue.config.productionTip = false

axios.defaults.baseURL=ApiUrl;

//在main.js中定义一个全局函数
Vue.prototype.ApiUrl =ApiUrl;
Vue.prototype.RouterBase =RouterBase;
if (process.env.NODE_ENV == "development") {
    axios.defaults.baseURL = '/api/';
    Vue.prototype.RouterBase ="";

}
const router = new VueRouter({
  mode: 'history',
  base:RouterBase,//服务器地址，不设置时，默认为服务器根目录下
  routes
})
router.beforeEach((to, from, next) => {
  iView.LoadingBar.start();
  if (to.matched.some(m => m.meta.auth)) {
    let users=['UMUser','VMUser','CMUser'];
    let user=users.filter(item=>item.substr(0,2)==to.path.substr(1,2)).join('');
    users.forEach(function (value){
        if(value!=user){
           sessionStorage.removeItem(value)
}
    })
    if (!to.query.url && from.query.url) {
      to.query.url = from.query.url
    }
    if (JSON.parse(sessionStorage.getItem(user))) {
      next()
    }
    else {
      if(to.path.indexOf("UM")>0)
      {
        next({path: '/UMlogin', query: {Rurl: to.fullPath}})
        return false;
      }
      if(to.path.indexOf("VM")>0)
      {
        next({path: '/VMlogin', query: {Rurl: to.fullPath}})
        return false;
      }
      if(to.path.indexOf("CM")>0)
      {
        next({path: '/CMlogin', query: {Rurl: to.fullPath}})
        return false;
      }
      else{
        next({path: '/UMlogin', query: {Rurl: to.fullPath}})
        return false;
      }
    }
  }
  else {
    next()
  }
})
router.afterEach(route => {
  iView.LoadingBar.finish();
});
new Vue({
  el: '#app',
  router,
  store,
  render: h => h(App)
})




