<template>
  <div class="content" :id="id"></div>
</template>

<script>
    import Cesium from 'Cesium';
    import  zlib from 'zlib'
    export default {
        name: "Viewer",
        props:{
          id:{
            type:String
          }
        },
        components:{
        },
        mounted(){
           this.init();

        },
         methods:{
            init(){
              if(typeof Cesium !== "undefined")
                {
                  this.$emit('onload',{Cesium,zlib})//调用超图入口文件
                }
            },
      }

    }
</script>

<style scoped>
  .content{
    position:relative;
    width:100%;
    height: 100%;
  }
</style>



