<template>
  <div>
    <ModulePage v-bind="patrolScheme"></ModulePage>
  </div>
</template>

<script>
import ModulePage from '../../../../components/Common/ModulePage'
export default {
  name: "UM_main",
  components:{ModulePage},
  data() {
    return {
        patrolScheme:{
          moduleName:"能耗管理",
          leftTree:[]
        },
        treeNodeJumpUrl:"/UM/TunnelEnergy/detials/",
    };
  },
  created(){
    this.patrolScheme.leftTree = [];
    this.axios.get("/tunnels").then(result => {
      let {code, data} = result.data;
      var _this=this;
      if (code == 200) {
        data.forEach(a=>{
          let temp={};
          temp.id=a.id;
          temp.name=a.name;
          temp.url=_this.treeNodeJumpUrl+a.id;
          // var child=[{id:1,name: '查询', url: '/UM/patrol/query'},
          //             {id:2,name: '制定', url: '/UM/patrol/add'}];
          //             temp.childNode= child;
          _this.patrolScheme.leftTree.push(temp);
        })
      }
    });
  },
};
</script>



