<template>
    <div style="padding: 10px">
        <div class="conditions">
            <Row>
                <Col span="6">
                    <span class="conditionTitle">入廊目的：</span>
                    <Select v-model="conditions.actionId"  @on-change='queryRecords()' style="width:60%">
                        <Option value=null>所有</Option>
                        <Option v-for="(item,index) in actions" :value="item.val" :key="index">{{ item.key }}</Option>
                    </Select>
                </Col>
                <Col span="6">
                    <span class="conditionTitle">申请状态：</span>
                    <Select v-model="conditions.statusId"  @on-change='queryRecords()' style="width:60%">
                        <Option value=null>所有</Option>
                        <Option v-for="(item,index) in status" :value="item.val" :key="index">{{ item.key }}</Option>
                    </Select>
                </Col>

                <Col span="6">
                    <span class="conditionTitle">开始时间：</span>
                    <DatePicker type="datetime" placeholder="请选择开始时间" style="width: 60%" v-model="conditions.startTime" @on-change="queryList()"></DatePicker>
                </Col>
                <Col span="6">
                    <span class="conditionTitle">结束时间：</span>
                    <DatePicker type="datetime" placeholder="请选择结束时间" style="width: 60%" v-model="conditions.endTime" @on-change="queryList()"></DatePicker>
                </Col>
            </Row>  
        </div>      
        <div class="list">
            <Table :columns="columns1" :data="applicationRecordList"></Table>
            <Modal
                title="参观人员信息"
                v-model="modal10"
                class-name="vertical-center-modal">
                <Table :columns="columns2" :data="visitorInfo" width="500" height="300"></Table>
            </Modal>
            <Modal
                title="申请状态"
                v-model="modal1"
                class-name="vertical-center-modal">
                <image-from-url :url="imageUrl"></image-from-url>
            </Modal>
            <Page :total="page.pageTotal" :current="page.pageNum" :page-size="page.pageSize" show-sizer show-total   
                    placement="top" @on-change="handlePage" @on-page-size-change='handlePageSize' show-elevator style="text-align: right;margin-top: 20px;"></Page>
        </div>
    </div>
</template>
<script>
import axios from "axios";
import ImageFromUrl from "../../../../components/Common/ImageFromUrl";
export default {
  components: {
    ImageFromUrl
  },
  data() {
    return {
      imageUrl: "",
      columns1: [
        {
          type: "index",
          align: "center",
          width: 60
        },
        {
          title: "申请人",
          key: "staff",
          align: "center",
          render: (h, params) => {
            return h("div", params.row.staff.name);
          }
        },
        {
          title: "入廊目的",
          key: "actionName",
          align: "center"
        },
        {
          title: "管廊名称",
          key: "tunnel",
          align: "center",
          render: (h, params) => {
            return h("div", params.row.tunnel.name);
          }
        },
        {
          title: "计划入廊时间",
          key: "preTime",
          align: "center",
          width: 150,
          render: (h, params) => {
            return h(
              "div",
              new Date(params.row.preTime).format("yyyy-MM-dd hh:mm:s")
            );
          }
        },
        {
          title: "申请状态",
          key: "statusName",
          align: "center",
          render: (h, params) => {
            let tmpStr = "";
            if (params.row.status == 3) {
              tmpStr = "拒绝";
            } else if (params.row.status == 2) {
              tmpStr = "通过";
            } else if(params.row.status == 1){
              tmpStr = "审核中";
            }else{
              tmpStr = "结束";
            }
            return h(
              "span",
              {
                style: {
                  color:
                    params.row.status == 3 ? "#ff6600"
                      : params.row.status == 2 ? "#19be6b" 
                      : params.row.status == 1 ? "#2d8cf0" : "#a005fdb3"
                }
              },
              tmpStr
            );
          }
        },
        {
          title: "查看申请状态",
          align: "center",
          render: (h, params) => {
            return h("div", [
              h(
                "Button",
                {
                  props: {
                    type: "primary",
                    size: "small"
                  },
                  style: {
                    marginRight: "5px"
                  },
                  on: {
                    click: () => {
                      this.showPic(params.row.id);
                    }
                  }
                },
                "查看"
              )
            ]);
          }
        },
        {
          title: "创建时间",
          key: "crtTime",
          align: "center",
          width: 150,
          render: (h, params) => {
            return h(
              "div",
              new Date(params.row.crtTime).format("yyyy-MM-dd hh:mm:s")
            );
          }
        },
        {
          title: "进入管廊时间",
          key: "enterTime",
          align: "center",
          width: 150,
          render: (h, params) => {
            return h(
              "div",
              new Date(params.row.enterTime).format("yyyy-MM-dd hh:mm:s")
            );
          }
        },
        {
          title: "离开管廊时间",
          key: "exitTime",
          align: "center",
          width: 150,
          render: (h, params) => {
            return h(
              "div",
              new Date(params.row.exitTime).format("yyyy-MM-dd hh:mm:s")
            );
          }
        },
        {
          title: "参观人数",
          key: "visitorNumber",
          align: "center"
        },
        {
          title: "所属公司",
          key: "company",
          align: "center",
          render: (h, params) => {
            return h("div", params.row.company.name);
          }
        },
        {
          title: "参观人员详细信息",
          width: 150,
          align: "center",
          render: (h, params) => {
            return h("div", [
              h(
                "Button",
                {
                  props: {
                    type: "primary",
                    size: "small"
                  },
                  style: {
                    marginRight: "5px"
                  },
                  on: {
                    click: () => {
                      this.show(params.row.id);
                    }
                  }
                },
                "信息"
              ),
              h(
                "Button",
                {
                  props: {
                    type: "error",
                    size: "small"
                  },
                  on: {
                    click: () => {
                      this.del(params.row.id);
                    }
                  }
                },
                "删除"
              )
            ]);
          }
        }
      ],
      applicationRecordList: [],
      modal10: false,
      modal1: false,
      visitorInfo: [],
      Pic: null,
      img: null,
      columns2: [
        {
          type: "index",
          align: "center",
          width: 30
        },
        {
          title: "姓名",
          key: "name",
          align: "center"
        },
        {
          title: "身份证",
          key: "idCard",
          align: "center",
          width: 200
        },
        {
          title: "联系方式",
          key: "tel",
          align: "center"
        }
      ],
      page: {
        pageNum: 1,
        pageSize: 10,
        pageTotal: 0
      },
      conditions: {
        tunnelId: null,
        actionId: null,
        startTime: null,
        endTime: null
      },
      tunnels: [],
      actions: [],
      status: [],
      tunnelId: null
    };
  },
  watch: {
    $route: function() {
      //2. $route发生变化时再次赋值planId
      this.tunnelId = this.$route.params.id;
      this.tunnels.forEach(a => {
        if (a.id == this.tunnelId) {
          this.queryRecords();
        }
      });
    }
  },
  computed: {
    params() {
      let param = {
        pageNum: this.page.pageNum,
        pageSize: this.page.pageSize,
        tunnelId: this.tunnelId,
        action: this.conditions.actionId,
        startTime: new Date(this.conditions.startTime).getTime(),
        endTime: new Date(this.conditions.endTime).getTime(),
        status: this.conditions.statusId
      };
      return Object.assign({}, param);
    }
  },
  mounted() {
    this.queryRecords(),
      axios.get("/tunnels ").then(response => {
        let { code, data } = response.data;
        if (code == 200) {
          this.tunnels = data;
        }
      });
    axios.get("/action-enums").then(response => {
      let { code, data } = response.data;
      if (code == 200) {
        this.actions = data;
      }
    });
    axios.get("/reqstatus-enums").then(response => {
      let { code, data } = response.data;
      if (code == 200) {
        this.status = data;
      }
    });
  },
  methods: {
    queryRecords() {
      axios.post("req-historys/datagrid", this.params).then(response => {
        let { code, data } = response.data;
        if (code == 200) {
          this.applicationRecordList = data.list;
          this.page.pageTotal = data.total;
        }
      });
    },
    handlePage(value) {
      this.page.pageNum = value;
      this.queryRecords();
    },
    handlePageSize(value) {
      this.page.pageSize = value;
      this.queryRecords();
    },
    del(id) {
      axios.delete("/req-historys/" + id).then(response => {
        this.applicationRecordList.splice(id, 1);
      });
    },
    show(id) {
      axios.get("/req-historys/" + id).then(response => {
        if (response.data != null) {
          // response.data.data.visitorInfo='张三,3256891532611154X,123568956452|实力三,3256891532611154X,123568956452'
          var arr = new Array();
          for (let index in response.data.data.visitorInfo.split(",")) {
            var str = response.data.data.visitorInfo.split(",")[index];
            arr.push(str);
          }
          var arr2 = new Array();
          for (let k in arr) {
            var obj = {
              name: arr[k].split("-")[0],
              idCard: arr[k].split("-")[1],
              tel: arr[k].split("-")[2]
            };
            arr2.push(obj);
          }
          this.visitorInfo = arr2;
        }
      });
      this.modal10 = true;
    },
    showPic(id) {
        this.imageUrl = "/req-historys/" + id + "/activiti-png";
        this.modal1 = true;
    }
  }
};
</script>
<style scoped>
.conditions {
  margin-bottom: 20px;
}
</style>



