<template>
  <div id="top" class="top">
    <div class="ImgResponsive lf" id="imgResponsive">
      <img class="img-responsive" :src="logo">
    </div>
    <div class="TopTitle lf"><span>{{title}}</span></div>
  </div>
</template>

<script>
  import { VM_TILTE, LOGO_IMAGE }  from '../../../../static/VM/js/VMGlobalConfig'

    export default {
        name:'top',
        data(){
            return {
                title:'',
                logo:''
            }
    },
        mounted(){
            this.init();
        },
        methods:{
            init(){
                let logo_img = LOGO_IMAGE ? LOGO_IMAGE : "MaxviewLogo";

                this.logo=require('../../../assets/VM/'+ logo_img +'.png') //logo image init
                this.title=VM_TILTE; //title init

            }
        }
  }
</script>

<style scoped>
  .top{
    width: 100%;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    /*皮肤一*/
    /*background-color: #001A27;*/
    /*皮肤二*/
    background-color: #031324;
    /*background: linear-gradient(to right,rgba(0,26,39,1),rgba(0,26,39,0.9),rgba(0,26,39,1));*/
    /*background-image: url('../../../assets/VM/');*/
    /*background-repeat: no-repeat;*/
    /*background-size: 100% 100%;*/
    /*overflow: hidden;*/
  }
  #top .ImgResponsive{
    width: 14%;
    height: 8%;
    position:absolute;
    top: 3%;
    /*z-index: 1003;*/
  }

  #top .TopTitle{
    height: 6%;
    width: 94%;
    overflow: hidden;
  }
  #top .TopTitle >span{
    font-family: inherit;
    display: inline-block;
    font-weight: 500;
    line-height: 1.1;
    margin-top: 0.5%;
    margin-bottom: 0.3%;
    color: rgba(200,242,250);
    font-size: 2rem;
    margin-left: 16%;
    text-shadow: 0 0 6px #1765B3;
  }
</style>



