<template>
    <div class="SimplePie" :id="id"></div>
</template>
<style scoped>
.SimplePie {
  position: relative;
  height: 100%;
  width: 100%;
}
</style>
<script>
export default {
  name: "SimplePie",
  props: {
    id: {
      default: ""
    },
    requestUrl: {
      default: ""
    },
    intervalTime: {
      default: 60000
    },
    title: {
      default: {
        text: "SimplePie",
        x: 'left',
      }
    },
  },
  data() {
    return {
      myChart: {},
      visualMapArr: [],
      option: {
        backgroundColor: '#eeeeee',
        title: {
          text: "Customized Pie",
          left: "left",
          textStyle: {
            color: '#000'
          }
        },

        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },

        visualMap: {
          show: false,
          min: '',
          max: '',
          inRange: {
            colorLightness: [0, 1]
          }
        },
        series: [
          {
            name: "各类型设备数",
            type: "pie",
            // radius: "55%",
            center: ["50%", "50%"],
            data: [
            ].sort(function (a, b) {
              return a.value - b.value;
            }),
            // roseType: "radius",
            label: {
              normal: {
                show: true
              },
              emphasis: {
                show: true
              }
            },
            lableLine: {
              normal: {
                show: true
              },
              animationType: 'scale',
              animationEasing: 'elasticOut',
              animationDelay: function (idx) {
                return Math.random() * 200;
              },
              emphasis: {
                show: true
              }
            },
            itemStyle: {
              shadowColor: 'rgba(128, 128, 128, 0.5)',
              shadowBlur: 10,
              shadowOffsetX:7,
              shadowOffsetY:7,
            }
          }
        ]
      }
    };
  },
  mounted() {
    this.init();
    this.refreshData();
  },
  methods: {
    init() {
      this.drawBar();
      this.fetchData(this.requestUrl);
    },
    drawBar() {
      let _this = this;
      _this.myChart = _this.$echarts.init(document.getElementById(_this.id));
      _this.option.title = _this.title;
      _this.myChart.setOption(_this.option);
      window.addEventListener("resize", _this.myChart.resize);
    },
    fetchData(requestUrl) {
      let _this = this;

      _this.axios.get(requestUrl).then(result => {
        let { code, data } = result.data;
        if (code == 200) {
          // _this.option.series[0].data = [];
          // data.forEach(element => {
          //   _this.option.series[0].data.push({
          //     name: element.key,
          //     value: element.val,
          //   });
          //   this.visualMapArr.push(element.val)
          //   _this.option.visualMap.min = Math.min.apply(null, this.visualMapArr)
          //   _this.option.visualMap.max = Math.max.apply(null, this.visualMapArr)
          // });
          // _this.myChart.setOption(_this.option)
          var tempCount = 0;
          data.filter(function (item) {
            tempCount += item.val;
          })
          let newData = data.map(curObj => {
            return {
              value: curObj.val,
              name: curObj.key + " " + (100 * curObj.val / tempCount).toFixed(2) + "%"
            }
          });
          if (JSON.stringify(newData) != JSON.stringify(data)) {
            _this.option.series[0].data = newData;
            _this.myChart.setOption(_this.option);
          }
        }
      });
    },
    //定时刷新数据
    refreshData() {
      let _this = this;
      setInterval(() => {
        _this.fetchData(_this.requestUrl);
      }, _this.intervalTime)
    },
  }
};
</script>



