<template>
    <div>
        <Row>
            <Col span="16">
                <span class="planDec">所属管廊</span>
                <Select v-model="condition.tunnelId" style="width: 150px;margin-right: 15px;font-size: 14px;" @on-change='conditionChange()'>
                    <Option value=null key="0">所有</Option>
                    <Option v-for="item in tunnels" :value="item.id" :key="item.id">{{ item.name }}</Option>
                </Select>
                <span class="planDec">缺陷类型</span>
                <Select v-model="condition.type" style="width: 150px;margin-right: 15px;" @on-change='conditionChange()'>
                    <Option value=null key="0">所有</Option>
                    <Option v-for="item in defectType" :value="item.key" :key="item.key">{{ item.value }}</Option>
                </Select>
                <span class="planDec">缺陷状态</span>
                <Select v-model="condition.status" style="width: 150px;margin-right: 15px;" @on-change='conditionChange()'>
                    <Option value=null key="0">所有</Option>
                    <Option v-for="item in defectStatus" :value="item.key" :key="item.key">{{ item.value }}</Option>
                </Select>
            </Col>
            <Col span="8" align="right">
            <Button class="btn" @click="conditionChange()">查询</Button>
            <Button class="btn">导出</Button>
            <!-- <Button class="btn" @click.native="goToMoudle({ path:'/UM/main/addPlan'})">添加</Button> -->
            </Col>
        </Row>

        <Row>
            <Table :columns='columns' :data="defects"></Table>
        </Row>
    </div>
</template>

<script>

import axios from "axios";
import Enum from '../../../../../static/Enum.json'

export default {
    name:"queryDefect",
    data() {
        return {
            columns:[
                {
                    title:"所属管廊",
                    key:"tunnelName"
                },
                {
                    title:"发现时间",
                    key:"createTime"
                },
                {
                    title:"缺陷类型",
                    key:"typeName"
                },
                {
                    title:"对象名",
                    key:"objName"
                },
                {
                    title:"危险等级",
                    key:"levelName"
                },
                {
                    title:"缺陷状态",
                    key:"statusName"
                },
                {
                    title:"描述",
                    key:"description"
                },
                {
                    title:"操作",
                    key:"action",
                    width:150,
                    align:'center',
                    render:(h, params) => {
                        return h('div', [
                            h('Button', {
                                props: {
                                    type: 'primary',
                                    size: 'small'
                                },
                                style: {
                                    marginRight: '5px'
                                },
                                on: {
                                    click: () => {
                                        this.enterOrder(params.index)
                                    }
                                }
                            }, '进入工单')
                        ]);
                    }
                }
            ],
            defects:[],
            tunnels: [],
            defectType: Enum.defectType,
            defectLevel: Enum.defectLevel,
            defectStatus: Enum.defectStatus,
            condition:{
                tunnelId: null,
                type: null,
                level: null,
                status: null,
                startTime: null,
                endTime: null,
            }
        }
    },
    mounted(){
        //从数据库读取select的option选项
        axios.get("/tunnels ").then(response =>{
            let { code, data } = response.data;
            if (code == 200) {
                this.tunnels = data;
            }
        });
    },
    components:{
    },
    methods: {
        // type 1:查看， 2：编辑  3：新增
        goToMoudle: function (index, type) {
            this.$router.push({
                name: 'UMDetailDefect',
                params: {
                    id: this.defects[index].id,
                    type: type,
                }
            });
        },
        //将选中的option传到父组件
        conditionChange(){
            axios.post("/defects/condition", this.condition).then(result=>{
                let {code, data} = result.data;
                if (code == 200){
                    this.defects = data;
                }
            });
        },
        enterOrder(index){
            console.log("enterOrder", this.defects[index]);
            // this.goToMoudle(index, types.pageType.Read);
        }
    }  
}
</script>




