<template >
      <div class="ThreeDimensionalContainer BackgroundImage">
          <!--<div class="ThreeDimensional" id="threeDimensional"></div>-->
          <sm-viewer
            :id="id"
            @onload="onload">
          </sm-viewer>
          <vm-title :title-text="title"></vm-title>
          <!--<Select-->
                  <!--@on-change="changeStatus"-->
                  <!--v-model="defaultOption">-->
              <!--<Option v-for="item in optionList" :value="item.value" :key="item.value">{{ item.label }}</Option>-->
          <!--</Select>-->
          <!--<Select-->
                  <!--class="Areas"-->
                  <!--v-if="showArea"-->
                  <!--@on-change="changeArea"-->
                  <!--v-model="defaultArea">-->
              <!--<Option v-for="item in areaList" :value="item.value" :key="item.value">{{ item.label }}</Option>-->
          <!--</Select>-->
          <select class="cd-select" id="list-dropdown" ></select>
          <select class="cd-select" id="area-dropdown" ></select>
      </div>
</template>

<style scoped>
    .ThreeDimensionalContainer >>> .ivu-select{
        position: absolute;
        top: 5%;
        left: 2%;
        width: 18%;
    }
    .ThreeDimensionalContainer >>> .ivu-select-dropdown,.ThreeDimensionalContainer >>> .ivu-select-selection{
        background-color: transparent;
    }
    .ThreeDimensionalContainer  >>> .ivu-select-selection{
        border: 1px solid #001A27;
    }
    .ThreeDimensionalContainer  >>>  .ivu-select-selection{
        border-color: #001A27;
    }
    .ThreeDimensionalContainer  >>> .ivu-select-selection:hover{
        border-color: #001A27;
    }
    .ThreeDimensionalContainer >>> .ivu-select:focus,.ThreeDimensionalContainer >>> .ivu-select-selection{
        border-color: #001A27;
        box-shadow: 0 0 0 2px rgba(0,26,39,.2);
    }
    .ThreeDimensionalContainer  >>>  .ivu-select-item, .ThreeDimensionalContainer  >>>  .ivu-select{
        color: #FFF;
    }
    .ThreeDimensionalContainer .ivu-select-item-selected, .ThreeDimensionalContainer  .ivu-select-item-selected:hover,.ThreeDimensionalContainer  .ivu-select-item:hover{
        background: #001A27;
    }

</style>
<style>
    .cesium-viewer-bottom{
        display:none
    }
    .ThreeDimensionalContainer .Areas{
        left: 20.5%;
    }
</style>

<script>
    import VmTitle from '../VMTitle'
    import SmViewer from '../../Common/3DViewer'
    import { URL_CONFIG } from '../../../../static/3DMap/js/3DMapConfig'
    import { setViewAngle } from '../../../scripts/commonFun'
    export default {
        data() {
            return {
                title:'三维系统',
                id:'threeDimensional',
                camera:{
                    longitude:116.4459063882812,
                    latitude:39.9194562827802,
                    height:1.1315100742734499,
                    roll:6.281842456921609,
                    pitch:-0.1422851080918648,
                    heading:4.798698976259086
                },
                timer:null,
                labelsID:[],
                addLabels:[],
                intervalID:null,
                optionList: [],//所有管廊
                areaList:[],//当前管廊下所有区域
                location:[],//缓存当前管廊下所有区域的位置
                defaultOption: '',
                defaultArea:'',
                showArea:false,
                scene:null,
                defaultAreaVal:1
            }
        },
        components:{
          VmTitle,
          SmViewer
        },
        mounted(){
            this.init();
        },
        watch:{
            optionList(){
                let _this=this;
                _this.addOption(_this.optionList,'list-dropdown');

                $('#list-dropdown').dropdown( {
                    gutter : 5,
                    stack : false,
                    slidingIn : 100,
                    onOptionSelect(opt){
                        _this.changeStatus(opt.attr('data-value'));
                    }
                } )
            },
            areaList(){
                let _this=this;
                _this.addOption(_this.areaList,'area-dropdown');

                $('#area-dropdown').dropdown( {
                    defaultClass:['cd-dropdown','Areas'],
                    gutter : 5,
                    stack : false,
                    slidingIn : 100,
                    onOptionSelect(opt){
                        _this.changeArea(opt.attr('data-value'));
                    }
                } );
            }
        },
        methods:{
            init(){
                this.fetchData();
                this.changeStatus(this.defaultAreaVal);
            },
            onload(parent){
                var _this=this;

                var  Cesium=parent.Cesium;
                // 初始化viewer部件
                var viewer = new Cesium.Viewer(this.id);

                var scene = viewer.scene;
                _this.scene=scene;

                var widget = viewer.cesiumWidget;

                try{
                  //打开所发布三维服务下的所有图层
                  var promise = scene.open(URL_CONFIG.BIM_SCP);
                   //获取相机
                    var camera=viewer.scene.camera;

                    // setInterval(()=>{
                    //     var camera=viewer.scene.camera;
                    //     var position=camera.position;
                    //     //将笛卡尔坐标化为经纬度坐标
                    //     var cartographic = Cesium.Cartographic.fromCartesian(position);
                    //     var longitude = Cesium.Math.toDegrees(cartographic.longitude);
                    //     var latitude = Cesium.Math.toDegrees(cartographic.latitude);
                    //     var height = cartographic.height;
                    //     console.log(longitude+"/"+latitude+"/"+height);
                    //     console.log('pitch'+camera.pitch)
                    //     console.log('roll'+camera.roll)
                    //     console.log('heading'+camera.heading)
                    // },10000)
                  Cesium.when(promise,function(layer){
                      //设置BIM图层不可选择
                      layer.forEach(curBIM=>curBIM._selectEnabled=false);
                      //设置相机位置、视角，便于观察场景
                      setViewAngle(scene,Cesium,_this.camera)

                  },function(e){
                    if (widget._showRenderLoopErrors) {
                      var title = '加载SCP失败，请检查网络连接状态或者url地址是否正确？';
                      widget.showErrorPanel(title, undefined, e);
                    }
                  });
                }
                catch(e){
                  if (widget._groundPrimitives) {
                    var title = '渲染时发生错误，已停止渲染。';
                    widget.showErrorPanel(title, undefined, e);
                  }
                }
                //滚轮滑动，获得当前窗口的经纬度，偏移角
                var handler = new Cesium.ScreenSpaceEventHandler(scene.canvas);
                handler.setInputAction(e=>{
                    this.stopRoll(this.getLocation,500,scene,viewer)
                },Cesium.ScreenSpaceEventType.WHEEL)

                // handler.setInputAction(e=>{
                //     var position=scene.pickPosition(e.position)
                //     var camera=viewer.scene.camera;
                //     var cartographic = Cesium.Cartographic.fromCartesian(position)
                //     var longitude = Cesium.Math.toDegrees(cartographic.longitude);
                //     var latitude = Cesium.Math.toDegrees(cartographic.latitude);
                //     var height = cartographic.height;
                //
                //     console.log(longitude+"/"+latitude+"/"+height);
                //     console.log('pitch'+camera.pitch)
                //     console.log('roll'+camera.roll)
                //     console.log('heading'+camera.heading)
                //
                // },Cesium.ScreenSpaceEventType.LEFT_CLICK)

            },
            stopRoll(callback,wait,scene ,viewer){
                var _this=this;

                clearTimeout(_this.timer);
                clearInterval(_this.intervalID);//清除定时更新实体集
                _this.timer=setTimeout(function(){
                    callback.call(_this,scene,viewer)
                        .then(result=>{
                            _this.labelsID.splice(0);//清空当前id数组
                            for(let i=0;i<result.length;i++){
                                let { longitude,latitude,height,name,id,deviation  }=result[i];
                                _this.labelsID.push(id)//保存当前最近lablesID值
                                viewer.entities.add({
                                    position: Cesium.Cartesian3.fromDegrees(parseFloat(longitude),parseFloat(latitude),parseFloat(height)),
                                    label:{
                                        text:name,
                                        fillColor:Cesium.Color.BLACK,
                                        outlineColor:Cesium.Color.BLACK,
                                        pixelOffset:new Cesium.Cartesian2(deviation,0.0),
                                        scaleByDistance:new Cesium.NearFarScalar(0,1,40,0)
                                    }
                                })
                            }

                            //更新实体值
                            if(viewer.entities.values&&_this.labelsID){
                                _this.intervalID=setInterval(()=>{
                                    _this.axios.post('/measobjs/ids',{ids:_this.labelsID.join(',')})
                                        .then(result=>{
                                            let { code, data } = result.data;

                                            if(code==200){
                                               for(let i=0;i<data.length;i++){
                                                   if(data[i].name!=viewer.entities.values[i]._label._text._value){
                                                       viewer.entities.values[i]._label._text._value=data[i].name;
                                                   }
                                               }
                                            }
                                        })
                                },1528437)
                            }
                        });
                },wait)
            },
            getLocation(scene ,viewer){
                return new Promise((resolve,reject)=>{
                    //将笛卡尔坐标化为经纬度坐标
                    var _cameras=scene.camera;

                    var cartographic = Cesium.Cartographic.fromCartesian(_cameras._position);
                    var longitude = Cesium.Math.toDegrees(cartographic.longitude);
                    var latitude = Cesium.Math.toDegrees(cartographic.latitude);
                    var height = cartographic.height;

                    if(height < 0) {
                        height = 0;
                    }
                    this.axios.post('/measobjs/gps',
                        {longitude:longitude,latitude:latitude,height:height})
                        .then(result=>{
                            let { code, data } = result.data;
                            if(code==200
                                &&data.length!=0
                                &&JSON.stringify(this.addLabels)!==JSON.stringify(data)){
                                    //缓存labels用于判断下次取到数据是否一致
                                    this.addLabels=data;
                                    resolve(data)
                                }
                        })
                })
            },
            fetchData(){
                let _this=this;
                _this.axios.get('/tunnels')
                    .then(result=>{
                        let { code,data }=result.data;
                        if(code==200){
                            _this.optionList.splice(0)//清空管廊数组
                            data.forEach(tunnel=>_this.optionList.push({value:tunnel.id,label:tunnel.name}));
                        }
                    })
            },
            changeStatus(val){
                let _this=this;

                _this.axios.get(`tunnels/${val}/areas/`)
                    .then(result=>{
                        let { code,data }=result.data;
                        if(code==200){
                            _this.showArea=true;
                            _this.areaList.splice(0)//清空管廊数组
                            _this.location.splice(0)//清空缓存位置
                            if(data){
                                data.reverse().forEach((area,index)=>{
                                    _this.areaList.push({value:index,label:area.name})
                                    _this.location.push(area.location)
                                })
                            }
                        }

                    })
            },
            changeArea(index){
                let _this=this;
                try{
                    let location =_this.location[index].split('/');

                    _this.scene.camera.flyTo({
                        destination : new Cesium.Cartesian3.fromDegrees(parseFloat(location[0]),parseFloat(location[1]),parseFloat(location[2])),
                        orientation : {
                            heading : parseFloat(location[3]),
                            pitch : parseFloat(location[4]),
                            roll : parseFloat(location[5])
                        }
                    })
                }catch (e){}

            },
            addARrea(){
                let _this=this;
                _this.axios.post('/areas',
                    {
                        "name":"区域-5",
                        "location":"116.4452364361199/39.91946138913283/1.2353791933507947/4.798691472818735/-0.142284992117379/6.281842456965787",
                        "tunnelId":1,
                        "crtTime":+new Date(),
                    }
                )
                    .then(result=>console.log(result))
                _this.axios.post('/areas',
                    {
                        "name":"区域-6",
                        "location":"116.44434558905607/39.9194540339786/0.9999636112648158/4.798681495376009/-0.14228435062067635/6.281842456932962",
                        "tunnelId":1,
                        "crtTime":+new Date(),
                    }
                )
                    .then(result=>console.log(result))
            },
            addOption(optionList,id='cd-dropdown'){
                var $dropdown =$('#'+id), optionList=optionList,optlist='';

                for(let i=0;i<optionList.length;i++){
                    optlist+=`<Option value="${optionList[i].value}" >${optionList[i].label}</Option>`
                }

                $dropdown.empty().append(optlist);

                $dropdown.children('option').eq(0).attr('selected','selected');//设置第一个为默认选中项

            },
        }
    }

</script>



