<template>
    <div>
      <h1>巡检计划信息总览</h1>
      <Row>
        <Col span="16">
          <Row>
            <Col span="24">
              <div class="GISbox">
                <div class="GISTitle">地图显示所有的管廊位置GIS</div>
                <sm-viewer
                  :id="id"
                  @onload="onload">
                </sm-viewer>
                <div class="tunnelProfile">
                  <div>设备信息概况</div>
                  <div>设备数：100</div>
                  <div>管廊1：70</div>
                  <div>管廊2：30</div>
                  <div>故障数：5</div>
                </div>
              </div>
            </Col>
            <Col span="11" style="margin-top: 5px">
              <Card>
                <p slot="title">故障信息列表</p>
                <p v-for="(item,index) in faultInfoList" value="item" :key="index">{{item.list}}</p>
              </Card>
            </Col>
            <Col span="11" offset="2" style="margin-top: 5px">
              <Card>
                <p slot="title">故障信息列表</p>
                <p v-for="(item,index) in faultInfoList" value="item" :key="index">{{item.list}}</p>
              </Card>
            </Col>
          </Row>
        </Col>
        <Col span="8">
        <Row>
          <Col span="12"><div class="data-box" style="margin-right: 0"><DataShow v-bind="equimentTotalNum"></DataShow></div></Col>
          <Col span="12"><div class="data-box"><DataShow v-bind="equimentRunNum"></DataShow></div></Col>
          <Col span="12"><div class="data-box" style="margin-right: 0"><DataShow v-bind="spareAFew"></DataShow></div></Col>
          <Col span="12"><div class="data-box"><DataShow v-bind="duration"></DataShow></div></Col>
        </Row>
        </Col>
      </Row>
      <Row style="padding-top: 5px;">
        <Col span="7">
            <div style="width: 24vw; height:22vh;" class="equipmentChartBox">
              <simple-bar v-bind="equipmentChart"></simple-bar>
          </div>
        </Col>
        <Col span="7">
            <div style="width:24vw; height:22vh;" class="equipmentChartDoubleColor">
                <simple-pie v-bind="equipmentChartDoubleColor"></simple-pie>
            </div>
        </Col>
        <Col span="5">
            <div class="circleBox" style="background: #2C6A69;">
                <i-circle :size="200"
                          :trail-width="4"
                          :stroke-width="8"
                          :percent="75"
                          stroke-linecap="square"
                          stroke-color="#fff" style="margin-top: 5px;">
                  <p class="demo-Circle-inner" style="font-size:22px;color: #fff">已完成任务占比</p>
                  <p class="demo-Circle-inner" style="font-size: 22px;color: #fff">75%</p>
                </i-circle>
            </div>
        </Col>
        <Col span="5">
            <div class="circleBox" style="background: #D5C7E1">
                <i-circle :size="200"
                          :trail-width="4"
                          :stroke-width="8"
                          :percent="25"
                          stroke-linecap="square"
                          stroke-color="#E5B500" style="margin-top: 5px;">
                  <p class="demo-Circle-inner" style="font-size:22px">本月计划占比</p>
                  <p class="demo-Circle-inner" style="font-size: 22px">25%</p>
                </i-circle>
            </div>
        </Col>
      </Row>
    </div>
</template>

<script>
import DataBox from "../../../../components/Common/Box/DataBox";
import DataShow from "../../../../components/Common/DataShow";
import SimpleBar from "../../../../components/Common/Chart/SimpleBarChart";
import SimplePie from "../../../../components/Common/Chart/SimplePieChart";
import Ring from "../../../../components/Common/Chart/RingChart";
import SmViewer from "../../../../components/Common/3DViewer";
import ProcessRing from "../../../../components/Common/ProcessRing";
import { URL_CONFIG } from "../../../../../static/3DMap/js/3DMapConfig";
import { setViewAngle } from "../../../../scripts/commonFun";
import annualPlan from "../../../../assets/UM/annualPlan.png";
import curMonthPlan from "../../../../assets/UM/curMonthPlan.png";
import processedPlan from "../../../../assets/UM/processedPlan.png";
import pendingPlan from "../../../../assets/UM/pendingPlan.png";
// import FeatureSample from '../../../../components/Common/Chart/FeatureSample'
export default {
  name: "equipmentMain",
  data() {
    return {
      id: "GIS_ID",
      camera: {
        longitude: 116.43709465365579,
        latitude: 39.91793569836651,
        height: 1245.5482069457155,
        roll: 6.281841926729911,
        pitch: -0.9120242487858476,
        heading: 1.5735504792701676
      },
      equimentTotalNum: {
        label: "年度计划数",
        value: 365,
        imgSrc: annualPlan,
        imgBac: "#8C91A7"
      },
      equimentRunNum: {
        label: "本月计划",
        value: 87,
        imgSrc: curMonthPlan,
        imgBac: "#7ca9a9"
      },
      spareAFew: {
        label: "已完成巡检任务数",
        value: "5",
        imgSrc: processedPlan,
        imgBac: "#112f6b"
      },
      duration: {
        label: "待巡检任务",
        value: "82",
        imgSrc: pendingPlan,
        imgBac: "#BD4932"
      },
      equipmentChart: {
        id: "equipmentChartId",
        requestUrl: "/equipments/type/count",
        title: "各管廊巡检计划数"
      },
      equipmentChartDoubleColor: {
        id: "equipmentCharDoubleColortId",
        requestUrl: "/equipments/type/count",
        title: {
          text: "各管廊巡检计划占比"
        }
      },
      processedPlan: {
        id: "processedPlan",
        explain: "已完成任务占比"
      },
      breakDown: {
        id: "breakDown",
        explain: "本月计划占比"
      },
      faultInfoList: [
        { list: "设备1故障信息" },
        { list: "设备10故障信息" },
        { list: "设备1故障信息" },
        { list: "设备10故障信息" },
        { list: "设备1故障信息" },
        { list: "设备10故障信息" },
        { list: "设备1故障信息" },
        { list: "设备10故障信息" },
        { list: "设备1故障信息" },
        { list: "设备10故障信息" },
        { list: "设备1故障信息" },
        { list: "设备10故障信息" }
      ],
      tunnelInfoList: [
        { list: "管廊1设备总数70，损坏3" },
        { list: "管廊1设备总数70，损坏3" },
        { list: "管廊1设备总数70，损坏3" },
        { list: "管廊1设备总数70，损坏3" },
        { list: "管廊1设备总数70，损坏3" },
        { list: "管廊1设备总数70，损坏3" }
      ]
    };
  },
  components: {
    DataBox,
    SimpleBar,
    SimplePie,
    Ring,
    DataShow,
    SmViewer,
    ProcessRing
    // FeatureSample
  },
  mounted() {},
  methods: {
    goToMoudle: function(path) {
      this.$router.push(path);
    },
    // 查询成功
    onQueryComplete: function(args) {
      console.log("complete", args);
    },
    // 查询失败
    processFailed: function(args) {
      alert("失败");
    },
    doSqlQuery: function(sql) {
      console.log("查询", sql);
      var getFeatureParam, getFeatureBySQLService, getFeatureBySQLParams;
      console.log("新变量", getFeatureParam, getFeatureBySQLService, getFeatureBySQLParams);
      getFeatureParam = new SuperMap.REST.FilterParameter({
        attributeFilter: sql
      });
      console.log("getFeatureParam", getFeatureParam);
      getFeatureBySQLParams = new SuperMap.REST.GetFeaturesBySQLParameters({
        queryParameter: getFeatureParam,
        toIndex: -1,
        datasetNames: ["结构框架"]
      });
      console.log("getFeatureBySQLParams", getFeatureBySQLParams);
      var url = URL_CONFIG.BIM_DATA;
      console.log('sql url', url);
      getFeatureBySQLService = new SuperMap.REST.GetFeaturesBySQLService(url, {
        eventListeners: {
          processCompleted: _this.onQueryComplete,
          processFailed: _this.processFailed
        }
      });
      getFeatureBySQLService.processAsync(getFeatureBySQLParams);
    },
    onload(parent) {
      let Cesium = parent.Cesium;
      let _this = this;

      // 初始化viewer部件
      var viewer = new Cesium.Viewer(this.id, {
        navigation: false //关闭导航控件
      });
      console.log("viewer", viewer);

      var scene = viewer.scene,
        widget = viewer.cesiumWidget,
        imageryLayers = viewer.imageryLayers,
        imagery_mec;

      var provider_mec = new Cesium.SuperMapImageryProvider({
        url: URL_CONFIG.IMG_MAP //墨卡托投影地图服务
      });
      imagery_mec = imageryLayers.addImageryProvider(provider_mec);

      try {
        //打开所发布三维服务下的所有图层
        var promise = scene.open(URL_CONFIG.BIM_SCP);

        promise.then(function(layer) {
          // 将东西设置成不可选择
          layer.forEach(element => {
            // 结构性框架的要查数据，非结构性框架的不用管了，也不能选择
            if (element.name.indexOf("结构框架") < 0) {
              element.selectEnabled = false;
            } else {
              // 设置查找参数
              element.setQueryParameter({
                url: URL_CONFIG.BIM_DATA,
                dataSourceName: "tunnel",
                dataSetName: "结构框架",
                keyWord: "SmID"
              });

              console.log("结构性框架", layer);
            }
          });
        });

        viewer.pickEvent.addEventListener(function(feature) {
          console.log("feature", feature);
          _this.doSqlQuery("1=1");
        });

        // lay是所有的数据集
        Cesium.when(
          promise,
          function(layer) {
            //设置相机位置、视角，便于观察场景
            setViewAngle(scene, Cesium, _this.camera);
            console.log("layer", layer);
            console.log("overGroundLayer", overGroundLayer);

            console.log("addEvent");
            viewer.pickEvent.addEventListener(function(feature) {
              console.log("111");
              console.log("feature", feature);
            });
          },
          function(e) {
            if (widget._showRenderLoopErrors) {
              var title =
                "加载SCP失败，请检查网络连接状态或者url地址是否正确？";
              widget.showErrorPanel(title, undefined, e);
            }
          }
        );
      } catch (e) {
        if (widget._showRenderLoopErrors) {
          var title = "渲染时发生错误，已停止渲染。";
          widget.showErrorPanel(title, undefined, e);
        }
      }
    }
  }
};
</script>
<style scoped>
.ivu-col-span-12 {
  padding: 10px;
}
h1,
.ivu-card >>> .ivu-card-head p,
.GISTitle {
  color: #2e739b;
}
.GISbox {
  width: 100%;
  height: 43vh;
  border: 1px solid #dddee1;
  display: inline-block;
  vertical-align: top;
  margin-top: 10px;
  border-radius: 4px;
}
.GISTitle {
  background-color: #f7f8f9;
  font-size: 18px;
  line-height: 35px;
  padding-left: 5px;
  z-index: 2;
  position: absolute;
  width: 100%;
}
.tunnelProfile {
  border-bottom: 1px solid #dddee1;
  position: absolute;
  bottom: 10px;
  right: 10px;
}
.tunnelProfile div {
  border: 1px solid #dddee1;
  border-bottom: none;
  line-height: 35px;
  text-align: center;
  padding-left: 10px;
  padding-right: 10px;
}
.circleBox {
  text-align: center;
  height: 22vh;
  width: 98%;
  display: inline-block;
  vertical-align: middle;
  border: 1px solid #dddee1;
  border-radius: 4px;
  background: #eeeeee;
}
.equipmentChartBox,
.equipmentChartDoubleColor {
  border: 1px solid #dddee1;
  border-radius: 4px;
  height: 22vh;
  width: 99%;
}
.ivu-card >>> .ivu-card-body {
  height: 9vh;
  overflow-y: auto;
}
.cesium-viewer-bottom {
  display: none;
}
</style>





