<template>
    <div>
      <Card>
        <p slot="title">{{objtypeName}}</p>
        <div style="background-color:#7D8481;height: 100px; ">
          <span style="float:left;color: #ffffff;margin-top: 3.2vh;width:40%;font-size: 18px; ">{{ObjName}}</span>
          <div style="float:left;position: fixed;margin-left: 110px;">
            <span style="line-height : 100px;position: fixed;margin-top: 0px; height: 100px;background-color: #8990A0; color: #ffffff;font-size: 80px;font-family: LED ">{{ObjVal}}</span>
          </div>
          <div style="float:right;margin-left: 5px;">
            <Icon type="thermometer" size="100" color="#94e6d8"></Icon>
          </div>
        </div>
      </Card>
    </div>
</template>

<script>
    export default {
        name: "show-simulated-data",
      props:['objtypeName','ObjName','ObjVal'],
    }
</script>

<style scoped>
  @font-face {
    font-family: 'LED';
    src: url('/static/VM/css/DS-DIGIT.TTF') format('truetype');
    font-weight: normal;
    font-style: normal;
  }
</style>



