<template>
    <div>
        <Row :gutter="16">
            <Col span="12">
                <data-box v-bind="boxTotal"></data-box>
            </Col>
            <Col span="12">
                <data-box v-bind="boxRun"></data-box>
            </Col>
        </Row>
        <Row>
            <div style="width:500px; height:500px;">
                <simple-bar v-bind="equipmentChart"></simple-bar>
            </div>
        </Row>
        <Row>
            <Button class="btn" @click.native="goToMoudle({ path:'/UM/QueryEquipment'})">更多</Button>
        </Row>
    </div>
</template>

<script>
import DataBox from '../../../../components/Common/Box/DataBox'
import SimpleBar from '../../../../components/Common/Chart/SimpleBarChart'
export default {
    name: 'equipmentMain',
    data() {
        return {
            boxTotal:{
                label: "设备总111数：",
                value: 100,
                imgSrc: "",
            },
            boxRun:{
                label: "运行中的设备数：",
                value: 87,
                imgSrc: "",
            },
            equipmentChart:{
                id:"equipmentChartId",
                requestUrl:"/equipments/type/count",
                title:"各类型设备数"
            }
        }
    },
    components: {
        DataBox,
        SimpleBar,
    },
    methods:{
        goToMoudle: function (path) {
            this.$router.push(path);
        },
    },
}
</script>




