Date.prototype.format = function(format) {
  var date = {
    "M+": this.getMonth() + 1,
    "d+": this.getDate(),
    "h+": this.getHours(),
    "m+": this.getMinutes(),
    "s+": this.getSeconds(),
    "q+": Math.floor((this.getMonth() + 3) / 3),
    "S+": this.getMilliseconds()
  };
  if (/(y+)/i.test(format)) {
    format = format.replace(RegExp.$1, (this.getFullYear() + '').substr(4 - RegExp.$1.length));
  }
  for (var k in date) {
    if (new RegExp("(" + k + ")").test(format)) {
      format = format.replace(RegExp.$1, RegExp.$1.length == 1
        ? date[k] : ("00" + date[k]).substr(("" + date[k]).length));
    }
  }
  return format;
}


export function getFormatTime() {
    var now = new Date();
    var date = now.format('yyyy年-MM月-dd日 hh:mm:ss').toString().split(" ")[0];
    var time = now.format('yyyy年-MM月-dd日 hh:mm:ss').toString().split(" ")[1];
    var week = "星期" + "日一二三四五六".charAt(now.getDay());
    var result = date + " " + week + " " + time;
    return result;
}



