import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

// 应用初始状态
const  state = {
  isLogin:0,          //初始时候给一个  isLogin=0  表示用户未登录
};

const mutations = {
  changeLogin(state,data){
    state.isLogin = data;
  }

};

// 创建 store 实例
export default new Vuex.Store({
    state,
    mutations
})



