<template>
  <div style="margin-top: 10px;margin-right: 24px">
    <Row>
      <Col span="12" style="padding-right: 20px;">
        <Form :model="plan" :label-width="100" @submit.native.prevent>
          <Col span="12">
            <FormItem label="计划编号：">
              <Input v-model='plan.id' :disabled='pageType==pageTypes.Read'></Input>
            </FormItem>
          </Col>
          <Col span="12">
            <FormItem label="计划名称：">
              <Input v-model='plan.name' :disabled='pageType==pageTypes.Read'></Input>
            </FormItem>
          </Col>
          <Col span="12">
            <FormItem label="所属管廊：">
              <Select v-model='plan.tunnelId' :disabled='pageType==pageTypes.Read'>
                <Option v-for="item in tunnels" :value="item.id" :key="item.id">{{ item.name }}</Option>
              </Select>
            </FormItem>
          </Col>
          <Col span="12">
            <FormItem label="计划状态：">
              <Select :disabled='pageType==pageTypes.Read'>
                <Option :value=0>所有</Option>
              </Select>
            </FormItem>
          </Col>
          <Col span="12">
            <FormItem label="责任班组：">
              <!-- <Select v-model='plan.inspectionGroup.id' :disabled='pageType==pageTypes.Read'>
                <Option v-for="item in groups" :value="item.id" :key="item.id">{{ item.name }}</Option>
              </Select> -->
            </FormItem>
          </Col>
          <Col span="12">
            <FormItem label="责任人：">
              <!-- <Input v-model="groups.find(a=>a.id==plan.inspectionGroup.id).leader.name" :disabled='pageType==pageTypes.Read'></Input> -->
            </FormItem>
          </Col>
          <Col span="12">
            <FormItem label="计划年月：">
              <Input v-model="plan.inspectTime" :disabled='pageType==pageTypes.Read'></Input>
            </FormItem>
          </Col>
          <Col span="12">
            <FormItem label="执行天数：">
              <Input v-model="plan.tasks.length" :disabled='pageType==pageTypes.Read'></Input>
            </FormItem>
          </Col>
          <Col span="24">
            <FormItem label="计划描述：">
              <Input type="textarea" :rows="4" placeholder="请输入描述..." :disabled='pageType==pageTypes.Read'></Input>
            </FormItem>
          </Col>
          <Col span="24">
            <FormItem label="审批记录：">
              <Input type="textarea" :rows="4" placeholder="请输入审批..." :disabled='pageType==pageTypes.Read'></Input>
            </FormItem>
          </Col>
        </Form>
        </Col>
        <Col span="12">
          <h3>巡检记录</h3>
          <ul class="patrolRecordUl">
            <li v-for="item in patrolRecords" :value="item.id" :key="item.id">{{item.id}}</li>
          </ul>
          <Table border stripe :columns="columns1" :data="plan.tasks" style="margin: 20px auto;"></Table>
          <div class="GIS">
            <sm-viewer
              :id="id"
              @onload="onload">
            </sm-viewer>
          </div>
        </Col>
        <Col span="24" v-show="pageType==pageTypes.Edit" style="text-align: right;">
          <Button type="primary" @click="submit()">确定</Button>
          <Button type="primary">取消</Button>
        </Col>
    </Row>
  </div>
</template>

<script>
import axios from "axios";
import types from '../../../../../static/Enum.json'
import SmViewer from '../../../../components/Common/3DViewer'
import { URL_CONFIG } from '../../../../../static/3DMap/js/3DMapConfig'
import { setViewAngle } from '../../../../scripts/commonFun'
export default {
  name: "plans",
  data() {
    return {
      id:'GIS_ID',
      // 页面类型 1：查看 2：编辑 3：新增
      pageType: 1,
      pageTypes: types.pageType,
      camera:{
        longitude:116.43709465365579,
        latitude:39.91793569836651,
        height:1245.5482069457155,
        roll:6.281841926729911,
        pitch:-0.9120242487858476,
        heading:1.5735504792701676
      },
      // plan:{
      //   id: 0,
      //   name: '',
      //   tunnelId: 0,
      //   groupId: 1,
      //   tasks: [],
      // },
      plan:[],
      // 管廊
      tunnels:[],
      groups:[
        {
          id:1,
          name:"group1",
          leader: {
            id: 0,
            name: '',
          }
        }
      ],
      patrolRecords: [
        {id:1},{id:2},{id:3},{id:4},{id:5},{id:6},{id:7},{id:8},{id:9},{id:10},
        {id:11},{id:12},{id:13},{id:14},{id:15},{id:16},{id:17},{id:18},{id:19},{id:20},
        {id:21},{id:22},{id:23},{id:24},{id:25},{id:26},{id:27},{id:28},{id:29},{id:30},
      ],
      columns1: [
          {
            title: '编号',
            key: 'id',
            align: 'center'
          },
          {
            title: '巡检人',
            key: 'inspectManName',
            align: 'center'
          },
          {
            title: '巡检时间',
            key: 'inspectTime',
            align: 'center'
          },
          {
            title: '巡检描述',
            key: 'planId',
            align: 'center'
          },
          {
            title: '计划名称',
            key: 'planName',
            align: 'center'
          },
          {
            title: '结果',
            key: 'status',
            align: 'center'
          },{
            title: '工作时间',
            key: 'taskTime',
            align: 'center'
          }
        ],
    };
  },
  components:{
    SmViewer
  },
  mounted(){
    this.plan.id = this.$route.params.id;
    this.pageType = this.$route.params.type;
    console.log(this.pageType)
    axios.get('inspection-plans/'+this.plan.id).then(response => {
        let { code, data} = response.data
        if(code == 200 ){
          this.plan = data;
          console.log('plan', this.plan);
          // 暂定默认值
          this.plan.createTime = new Date(this.plan.createTime).format("yyyy-MM");
          this.plan.inspectTime = new Date(this.plan.inspectTime).format("yyyy-MM");
          this.plan.tasks.forEach(element => {
            element.inspectTime = new Date(element.inspectTime).format("yyyy-MM-dd");
            element.taskTime = new Date(element.taskTime).format("yyyy-MM-dd hh:mm:ss")
          });
        }
    });

    // 获取所有的管廊
    axios.get("/tunnels ").then(response =>{
        let { code, data } = response.data;
        if (code == 200) {
            this.tunnels = data;
        }
    });

    // 从数据库读取责任班组select的option选项
    axios.get("/inspection-groups").then(response => {
      let { code, data } = response.data;
      if (code == 200) {
        this.groups = data;
      }
    });
  },
  watch:{
    '$route': function () {
      //2. $route发生变化时再次赋值planId
      this.plan.id = this.$route.params.id;
    }
  },
  methods:{
    onload(parent) {
      let Cesium = parent.Cesium,
          _this=this;

      // 初始化viewer部件
      var viewer = new Cesium.Viewer(this.id,{
        navigation:false //关闭导航控件
      });
      var scene = viewer.scene,
        widget = viewer.cesiumWidget,
        imageryLayers = viewer.imageryLayers,
        imagery_mec;

      var provider_mec = new Cesium.SuperMapImageryProvider({
        url : URL_CONFIG.IMG_MAP//墨卡托投影地图服务
      });
      imagery_mec = imageryLayers.addImageryProvider(provider_mec);


      try{
        //打开所发布三维服务下的所有图层
        var promise = scene.open(URL_CONFIG.BIM_SCP);
        Cesium.when(promise,function(layer){

          var overGroundLayer = scene.layers._layerQueue[0];
          //设置相机位置、视角，便于观察场景
          setViewAngle(scene,Cesium,_this.camera);
        },function(e){
          if (widget._showRenderLoopErrors) {
            var title = '加载SCP失败，请检查网络连接状态或者url地址是否正确？';
            widget.showErrorPanel(title, undefined, e);
          }
        });
      }
      catch(e){
        if (widget._showRenderLoopErrors) {
          var title = '渲染时发生错误，已停止渲染。';
          widget.showErrorPanel(title, undefined, e);
        }
      }
    },
  }
}
</script>

<style  scoped>
.patrolRecordUl li{
  list-style: none;
  display: inline-block;
  width: 25px;
  height: 25px;
  border: 1px solid #eeeeee;
  text-align: center;
}
.GIS{
  width: 100%;
  height: 28vh;
  float: right;
  text-align: center;
}
</style>



