<template>
  <Form :model="addEnterGalleryApplication" :label-width="120" @submit.native.prevent>
    <!-- <FormItem label="申请人所属公司：">
      <Input v-model="addEnterGalleryApplication.staffCompany" readonly></Input>
    </FormItem>
    <FormItem label="申请人所属部门：">
      <Input v-model="addEnterGalleryApplication.positionId" readonly></Input>
    </FormItem> -->
    <FormItem label="申请人：">
      <Input v-model="addEnterGalleryApplication.staff.name" readonly></Input>
    </FormItem>
    <FormItem label="管廊：">
      <Input v-model="addEnterGalleryApplication.tunnel.name" readonly></Input>
    </FormItem>
    <FormItem label="入廊目的：">
      <Input v-model="addEnterGalleryApplication.actionName" readonly></Input>
    </FormItem>
    <FormItem label="计划入廊时间：">
      <DatePicker type="datetime" placeholder="请选择计划入廊时间" v-model="addEnterGalleryApplication.preTime" readonly style="width: 560px"></DatePicker>
    </FormItem>
    <FormItem label="进入管廊时间：">
      <DatePicker type="datetime" placeholder="请选择进入管廊时间" v-model="addEnterGalleryApplication.enterTime" readonly style="width: 560px"></DatePicker>
    </FormItem>
    <FormItem label="访客总人数：">
      <InputNumber  :min="1" v-model="addEnterGalleryApplication.visitorNumber" style="width: 560px;"></InputNumber>
    </FormItem>
    <FormItem label="访客所属公司">
      <Input v-model="addEnterGalleryApplication.company.name" readonly></Input>
    </FormItem>
    <FormItem label="访客详细信息：">
      <Table border :columns="columns1" :data="addEnterGalleryApplication.visitorInfo"></Table>
    </FormItem>
    <FormItem label="备注：">
      <Input type="textarea" v-model="addEnterGalleryApplication.remark" :autosize="{minRows: 2,maxRows: 5}" placeholder="请输入审批备注"></Input>
    </FormItem>
    <FormItem label="审批意见：">
      <Button type="success"  @click="agree">同意</Button>
      <Button type="error" style="margin-left: 8px" @click="disagree">不同意</Button>
    </FormItem>
    <!-- <FormItem style="text-align: center">
      <Button type="primary" @click="submitEnterGalleryApplication">提交</Button>
      <Button type="ghost"  style="margin-left: 8px">取消</Button>
    </FormItem> -->
  </Form>
</template>
<script>
  import axios from "axios";
  export default {
    data(){
      return{
        addEnterGalleryApplication:{
          id: null,
          staff:{
            name: null
          },
          tunnel:{
            name: null            
          },
          actionName: null,
          preTime:  null,
          enterTime: null,
          visitorNumber: null,
          company: {
            name: null
          },
          positionId: null,
          visitorInfo:[] ,
          remark: null,
          processDefinitionId: null
        },
        columns1: [
          {
            title: '姓名',
            key: 'name',
            align: 'center'
          },
          {
            title: '身份证号',
            key: 'idCard',
            align: 'center'
          },
          {
            title: '联系方式',
            key: 'tel',
            align: 'center'
          }
        ],
      }
    },
    computed:{
      params() {
        let param = {
          remark: this.addEnterGalleryApplication.remark,
          id: this.addEnterGalleryApplication.id,
          processDefinitionId: this.addEnterGalleryApplication.processDefinitionId,
          value: 'agree'
        }
        return Object.assign({}, param);
      },
      paramsDis(){
        let param = {
          remark: this.addEnterGalleryApplication.remark,
          id: this.addEnterGalleryApplication.id,
          processDefinitionId: this.addEnterGalleryApplication.processDefinitionId,
          value: 'disagree'
        }
        return Object.assign({}, param);
      } 
    },
    mounted(){
      axios.get('/users/activiti/task/detail/'+this.$route.params.processDefinitionId+'/'+this.$route.params.processType).then(response=>{
        let{ code,data } = response.data
            if(code=200){
                this.addEnterGalleryApplication = data
                var arr = new Array()
                for(let index in response.data.data.visitorInfo.split(',')){
                    var str = response.data.data.visitorInfo.split(',')[index]
                    arr.push(str)
                }
                var arr2 = new Array()
                for(let k in arr){
                  var obj = {
                    name: arr[k].split('-')[0],
                    idCard: arr[k].split('-')[1],
                    tel: arr[k].split("-")[2]
                  }
                }
                arr2.push(obj)
                this.addEnterGalleryApplication.visitorInfo = arr2
                this.addEnterGalleryApplication.preTime = new Date(this.addEnterGalleryApplication.preTime).format('yyyy-MM-dd hh:mm:s')
                this.addEnterGalleryApplication.enterTime = new Date(this.addEnterGalleryApplication.enterTime).format('yyyy-MM-dd hh:mm:s')                
            }
      })
    },
    methods:{
      agree(){
        axios.post('users/activiti/req-historys/audit',(this.params)).then(response=>{
          let{ code,data } = response.data
          if(code==200){
            this.$router.push('/UM/myNews/queryMyTask')
          }
        })
      },
      disagree(){
        axios.post('users/activiti/req-historys/audit',(this.paramsDis)).then(response=>{
          let{ code,data } = response.data
          if(code==200){
            this.$router.push('/UM/myNews/queryMyTask')
          }
        })
      }
    }
  }
</script>
<style scoped>
  .ivu-form.ivu-form-label-right{
    width: 680px;
    margin: 20px auto;
  }
  ul li{
    list-style: none;
  }
  button{
    width: 80px;
  }
</style>




