<template>
  <div class="queryPlan">
    <div class="top-div">
      <Row>
        <Col span="6">
          <span class="planDec">选择年份：</span>
          <DatePicker type="year" placeholder="请选择年份" style="width:60%"></DatePicker>
        </Col>
        <Col span="6">
          <span class="planDec">巡检状态：</span>
          <Select v-model="conditions.status"  @on-change='conditionChange()' style="width:60%">
            <Option value=null key="0">所有</Option>
            <Option v-for="item in workOrderStatus" :value="item.key" :key="item.key">{{ item.value }}</Option>
          </Select>
        </Col>
        <Col span="6">
          <span class="planDec">月份时间：</span>
          <DatePicker type="month" placeholder="请选择月份" style="width:60%"></DatePicker>
        </Col>
        <Col span="6">
          <span class="planDec">所属小组：</span>
          <Select v-model="conditions.name"   @on-change='conditionChange()' style="width:60%">
            <Option value="">所有</Option>
            <Option v-for="item in Groups" :value="item.id" :key="item.id">{{ item.name }}</Option>
          </Select>
        </Col>
      </Row>
    </div>
    <div  class="bottom-div" ref="bottomDiv">
      <!-- <Scroll :on-reach-bottom="handleReachBottom"  height="777px;"> -->
      <Row class="detailsList">
        <Col span="6" dis-hover v-for="(item,index) in plans" :key="item.index" ref="planBox">
          <div class="everyList">
            <div class="detailsInfo">
              <div class="icon">
                <Icon type="leaf" size="24" color="#fff"></Icon>
              </div>
              <div class="details">
                <div class="details-top">
                  <div class="tunnelName">{{item.tunnelName}}</div>
                  <div class="planName">{{item.name}}</div>
                </div>
                <div class="details-bottom">
                  <div>
                    <p class="planStatus">任务状态</p>
                    <p class="planStatusDec">{{item.status}}完成</p>
                  </div>
                  <div>
                    <p class="patrolGroup">巡检小组</p>
                    <p class="patrolGroupName">{{item.groupName}}</p>
                  </div>
                </div>
              </div>
            </div>
            <Row class="option">
              <Col span="6">
                <span @click="show(index)">详情</span>
              </Col>
              <Col span="6">
                <span  @click="edit(index)">编辑</span>
              </Col>
              <Col span="6">xx</Col>
              <Col span="6">xx</Col>
            </Row>
          </div>
        </Col>
      </Row>
      <!-- </Scroll> -->
    <Page :total="page.pageTotal" :current="page.pageNum" :page-size="page.pageSize" show-sizer show-total   
                    placement="top" @on-change="handlePage" @on-page-size-change='handlePageSize' show-elevator style="text-align: right;margin-top: 20px;"></Page>   
    </div>
  </div>
</template>
<script>
import axios from "axios";
import Enum from "../../../../../static/Enum.json";
// import func from './vue-temp/vue-editor-bridge';

export default {
  data() {
    return {
      plans: [],
      tunnelId: 1,
      tunnels: [],
      Groups: [],
      pageSizeComputed: null,
      workOrderStatus: Enum.patrolPlanOrderStatus,
      conditions: {
        tunnelId: null,
        workOrderStatusVal: null,
        name: null
      },
      page: {
        pageNum: 1,
        pageSize: 10,
        pageTotal: 0
      },
    };
  },
  watch: {
    '$route': function () {
      //2. $route发生变化时再次赋值planId
      this.tunnelId = this.$route.params.id;
      this.tunnels.forEach(a => {
        if (a.id == this.tunnelId) {
          this.computedPageSize();
          this.conditionChange();
        }
      });
    }
  },
  computed: {
    params() {
      let param = {
        pageNum: this.page.pageNum,
        pageSize: this.pageSizeComputed,
        tunnelId: this.tunnelId,
        name: this.conditions.name,
        workOrderStatusVal: this.conditions.workOrderStatusVal
      };
      return Object.assign({}, param);
    }
  },
  mounted() {
    // 从数据库读取select的option选项
    axios.get("/tunnels ").then(response => {
      let { code, data } = response.data;
      if (code == 200) {
        this.tunnels = data;
      }
    });
    //从数据库中读取所有的小组option选项
    axios.get("/inspection-groups").then(response => {
      let { code, data } = response.data;
      if (code == 200) {
        this.Groups = data;
      }
    });
    this.computedPageSize();
    this.conditionChange();
  },
  methods: {
    conditionChange: function() {
      axios.post("inspection-plans/datagrid", (this.params)).then(response => {
          let { code, data } = response.data;
          if (code == 200) {
            for (let index in data.list) {
              data.list[index].createTime = new Date(data.list[index].createTime).format(
                "yyyy-MM-dd"
              );
              data.list[index].inspectTime = new Date(
                data.list[index].inspectTime
              ).format("yyyy-MM-dd");
            }
            this.plans = data.list;
            this.page.pageTotal = data.total;
          }
        });
    },
    computedPageSize(){
      var bottomDivH = this.$refs.bottomDiv.offsetHeight
      this.pageSizeComputed = Math.floor(bottomDivH/188)*4
      return this.pageSizeComputed
      console.log(this.pageSizeComputed)
    },
    goToMoudle: function(path) {
      this.$router.push(path);
    },
    // type 1:查看， 2：编辑
    goToMoudle2: function(index, type) {
      this.$router.push({
        name: "UMPatrolDetails",
        params: {
          id: this.plans[index].id,
          type: type
        }
      });
    },
    show(index) {
      this.goToMoudle2(index, Enum.pageType.Read);
    },
    edit(index) {
      this.goToMoudle2(index, Enum.pageType.Edit);
    },
    handlePage(value) {
        this.page.pageNum = value;
        this.conditionChange();
    },
    handlePageSize(value) {
        this.page.pageSize = value;
        this.conditionChange();
    },
  }
};
</script>
<style scoped>
.bottom-div {
  height: 100%;
}
.planDec {
  margin-right: 5px;
}
.detailsList .everyList{
  border: 1px solid #e8e8e8;
  width: 80%;
  margin-bottom: 20px;
}
.detailsInfo{
  height: 80%;
}
.icon,.details,.details-bottom div,.details-top div{
  display: inline-block;
}
.icon{
  background-color: #009DFF;
  width: 30px;
  height: 30px;
  border-radius: 100%;
  vertical-align: top;
  margin-top: 15px;
  margin-left: 5%;
  margin-right: 2px;
}
.ivu-icon{
    margin-left: 5px;
    margin-top: 1px;
}
.details-top{
  padding: 20px 4% 5px;
  font-size: 16px;
  font-weight: 700;
}
.planStatusDec,.patrolGroupName{
  font-size: 22px;
}
.details-bottom div{
  margin: 10px;
}
.planStatus,.patrolGroup{
  color:#a4a4a4;
  font-size: 14px;
  margin-bottom: 2px;
}
.option{
  background: #F7F9FA;
  line-height: 30px;
  font-size: 16px;
  color: #979696;
  border-top: 1px solid #e8e8e8
}
.option .ivu-col-span-6{
  border-right: 1px solid #e8e8e8;
  margin: 5px 0px;
  text-align: center;
  cursor: pointer;
}
  .queryPlan{
    height: 100%;
    padding: 70px 0px 0px 10px;
  }
  .top-div{
    height: 70px;
    margin: -70px 0 0;
  }
</style>





