<template>
  <div>
    <ModulePage v-bind="VideoMonitoring"></ModulePage>
  </div>
</template>

<script>
import ModulePage from '../../../../components/Common/ModulePage'
export default {
  name: "UM_main",
  components:{ModulePage},
  data() {
    return {
        VideoMonitoring:{
          moduleName:"视频监控系统",
          leftTree:[]
        },
        treeNodeJumpUrl:"/UM/VideoMonitoring/details",
    };
  },
  created(){
    this.VideoMonitoring.leftTree = [];
    this.axios.get("/tunnels").then(result => {
      let {code, data} = result.data;
      var _this=this;
      if (code == 200) {
        data.forEach(a=>{
          let temp={};
          temp.id=a.id;
          temp.name=a.name;
          temp.url=_this.treeNodeJumpUrl;
          var child=[
                      {id:2,name: '详情', url: '/UM/VideoMonitoring/details'}];
                      temp.childNode= child;
          _this.VideoMonitoring.leftTree.push(temp);
        })
      }
    });
  },
};
</script>


