<template>
 <div class="modal-mask" v-show="show" transition="modal" >
  <div :style="{top:top,left:left,position:'fixed'}" >
   <div class="modal-container">
     <!--header内容-->
    <div class="modal-header">
    <slot name="header">  <h2 slot="header" style="color: #22a5f5">详细信息</h2> </slot>
     </div>
     <!--body内容-->
     <div class="modal-body">
     <slot name="body">
     <ul style="    list-style: none;">
       <li  v-for=" item in data" :key="item.key"><span class="contentkey">{{item.key}}</span>
       <span class="contentVal">{{item.val}}</span>
         <hr>
     </li>
     </ul>
     </slot>
       <Button class="modal-default-button" type="info" @click="change">OK</Button>
     </div>
     <!--foot内容-->
     <div class="modal-footer">
     <slot name="footer">
     </slot>
     </div>
      </div>
       </div>
       </div>
</template>

<script>
    export default {
        name: "show-map-data-modal",
      data(){
          return{
        status:this.show,
        }},
      watch: {
        show(val) {
          this.status = val;//新增show的watch，监听变更并同步
        }
      },
      props: {
        show: {
          type: Boolean,
          required: true, // 必需且是bool类型的
          twoWay: true   // 双向绑定
        },
        top:{
          default:0
        }, //鼠标点击位置top
        left:{
          default:0
        },//鼠标点击位置left
        data:{
          default:[{
            key:"监测对象1",
            val:12.34
          }]
        }
      },
      methods: {
        change() {
          this.status=false;
          this.$emit('changeModalStatus',this.status);
        }
      },
    }
</script>

<style scoped>
  .contentkey{
    font-size: 14px;
    color: #828282;
  }
  .contentVal{
    color: #828282;
    font-size: 14px;
    position:relative;;
    float: right;
    right: 10px;
  }
  .modal-mask { position: fixed; z-index: 9998; top: 0; left: 0; width: 100%; height: 100%; background-color: rgba(0, 0, 0, .5); display: table; transition: opacity .3s ease; }
  .modal-container {
    border: 1px solid #197db3;
    border-radius: 8px;
    box-shadow: 5px 6px 4px rgba(0, 0, 0, .2);
    width: 300px;
    margin: 0px auto;
    padding: 20px 30px;
    background-color: #fff;
    transition: all .3s ease;
    font-family: Helvetica, Arial, sans-serif;
  }
  .modal-header h3 { margin-top: 0; color: #42b983; } .modal-body { margin: 20px 0; }
  .modal-default-button {
    float: right;
    top: 5px;
    position: relative;
  }
 .modal-enter, .modal-leave { opacity: 0; }
  .modal-enter .modal-container, .modal-leave .modal-container { -webkit-transform: scale(1.1); transform: scale(1.1); }
</style>



