<template>
  <div>
    <ModulePage v-bind="patrolScheme"></ModulePage>
  </div>
</template>
<style scoped>
</style>
<script>
  import ModulePage from '../../../../components/Common/ModulePage'
  export default {
    name:"OperatingSpaceMain",
    data() {
      return {
         patrolScheme:{
          moduleName:"空间管理",
          leftTree:[]
        },
        treeNodeJumpUrl:"/UM/OperatingSpace/tunnel/",
      };
    },
    components: {ModulePage},
    created() {
      this.patrolScheme.leftTree = [];
      this.axios.get("/tunnels ").then(result => {
        let {code, data} = result.data;
        var _this=this;
        if (code == 200) {
          data.forEach(a=>{
          let temp={};
          temp.id=a.id;
          temp.name=a.name;
          temp.url=_this.treeNodeJumpUrl+a.id;
          // var child=[{id:1,name: '查询', url: '/UM/patrol/query'},
          //             {id:2,name: '制定', url: '/UM/patrol/add'}];
          //             temp.childNode= child;
          _this.patrolScheme.leftTree.push(temp);
        })
      }
      });
     
    },
    // mounted() {
    //   this.screenWidth = document.body.clientWidth;
    //   this.screenHeight = document.body.clientHeight;
    //   this.goToMoudle({path: "/UM/OperatingSpace/tunnel/1"});

    // },
    // computed: {
    //   menuitemClasses: function () {
    //     return ["menu-item", this.isCollapsed ? "collapsed-menu" : ""];
    //   },

    // },
    // methods: {
    //   //跳转模块
    //   goToMoudle(path) {
    //     this.$router.push(path);
    //   },
    // }
  }
  ;
</script>





