<template>
  <div>
    <Card>
      <p slot="title">{{objtypeName}}</p>
      <div style="background-color:#7D8481;height: 100px; ">
        <span style="float:left;color: #ffffff;margin-top: 3.2vh;width:40%;font-size: 18px; ">{{ObjName}}</span>
        <div style="float:left;position: fixed;margin-left: 120px;margin-top:20px;">
          <i-switch style="position: fixed; width: 120px;height: 50px;" v-model="status"  @on-change="change">
            <span slot="open" style="font-size: 20px;top:15px;position: absolute">ON</span>
            <span slot="close" style="font-size: 20px;top:15px;position: absolute;left: 48px;">OFF</span>
          </i-switch>
        </div>
        <div style="float:right;margin-left: 5px;">
          <img style="width:100px;height:100px;" v-bind:src="imgUrl"/>
          <!--<Icon type="ios-settings" size="100" color="#94e6d8"></Icon>-->
        </div>
      </div>
    </Card>
  </div>
</template>

<script>
  export default {
    name: "show-switch-data",
    props:{
      id:{
        dafault:0,
      },
      objtypeName:{
        type:String,
        dafault:""
      },
      ObjName:{
        type:String,
        dafault:""
      },
      ObjVal:{
        dafault:null
      },
      imgUrl:{
        type:String,
        default:""
      },
      callback:{
        type:Function,
        default:function () {
        }
      }
    },
    data: function () {
      return {
        status: this.ObjVal
      };
    },
      methods: {
        change() {
          this.$emit('changeStatus',this.id,this.status);
        }
    },
    watch: {
      ObjVal(val) {
        this.status = val;//新增ObjVal的watch，监听变更并同步
      }
    },
    }
</script>

<style scoped>
  .ivu-switch:after {
    width: 70px;
    height: 50px;
    top: 0px;
  }
  .ivu-switch-checked:after {
    left: 50px;
  }
  @font-face {
    font-family: 'LED';
    src: url('/static/VM/css/DS-DIGIT.TTF') format('truetype');
    font-weight: normal;
    font-style: normal;
  }
</style>



