<template>
  <div>
    <ModulePage v-bind="plans"></ModulePage>
  </div>
</template>

<script>
import ModulePage from "../../../../components/Common/ModulePage";
export default {
  name: "UM_plan",
  components: { ModulePage },
  data() {
    return {
      plans: {
        moduleName: "预案管理",
        leftTree: []
      },
      treeNodeJumpUrl: "/UM/plans/query"
    };
  },
  created() {
    this.plans.leftTree = [];
    // this.axios.get("/tunnels").then(result => {
    //   let {code, data} = result.data;
    //   var _this=this;
    //   if (code == 200) {
    //     data.forEach(a=>{
    //       let temp={};
    //       temp.id=a.id;
    //       temp.name=a.name;
    //       temp.url="/UM/plans/query/"+a.id;
    //       _this.plans.leftTree.push(temp);
    //     })
    //   }
    // });
    for (let i = 1; i < 7; i++) {
      let element = {};
      element.id = i;
      switch (i) {
        case 1:
          element.name = "制定";
          break;

        case 2:
          element.name = "消防预案";
          break;

        case 3:
          element.name = "通风预案";
          break;

        case 4:
          element.name = "排水预案";
          break;

        case 5:
          element.name = "电气火灾预案";
          break;

        case 6:
          element.name = "人员出入预案";
          break;

        default:
          break;
      }
      element.url = "/UM/plans/add";

      this.plans.leftTree.push(element);
    }
  }
};
</script>


