<template>
  <div class="layout">
    <Layout class="coment">
      <Sider :width="screenWidth*0.14" collapsible :collapsed-width="78" v-model="isCollapsed" :style="{background: '#1D5F87'}">
        <Menu active-name="1-1" width="auto" :class="menuitemClasses" :open-names="['1']">
          <Submenu name="1">
            <template slot="title">
              <Icon type="ios-navigate"></Icon>
              <span>{{moduleName}}</span>
            </template>
            <div v-for="item in leftTree" :key="item.key" :name="item.id">
              <div v-if="item.childNode && item.childNode.length>0">
                <Submenu name="2">
                  <template slot="title">
                    <Icon type="ios-paper"></Icon>
                    <span>{{ item.name}}</span>
                  </template>
                  <MenuItem v-for="child in item.childNode" :key="child.id" :name="child.id" @click.native="goToMoudle({ path:child.url})">
                    <span> {{child.name}}</span>
                  </MenuItem>
                </Submenu>
              </div>
              <div v-else>
                <MenuItem name="item.id" @click.native="goToMoudle({ path:item.url})">
                  <span> {{item.name}}</span>
                </MenuItem>
              </div>
            </div>
          </Submenu>
        </Menu>
      </Sider>
      <Layout>
        <!-- <Content :style="{ padding: '15px', paddingBottom: '0px', backgroundImage:'url(' + img + ')',backgroundSize: 'cover' }"
                 style="overflow-y: auto;overflow-x: hidden;height: 200px;">
          <router-view></router-view>
        </Content> -->
         <Content :style="{ padding: '15px', paddingBottom: '0px',backgroundSize: 'cover' }"
                 style="overflow-y: auto;overflow-x: hidden;height: 200px;">
          <router-view></router-view>
        </Content>
        <Footer class="layout-footer-center">2009-2018 &copy; Bandweaver</Footer>
      </Layout>
    </Layout>
  </div>
</template>

<script>
// import BG from '../../assets/UM/BG.jpg'
export default {
  name: "mudulePage",
  props: {
    moduleName: {
      default: "moduleName"
    },
    leftTree: {
      default: [
        {
          id: 1,
          name: "管廊1",
          url: "",
          childNode: [
            {
              id: 11,
              name: "管廊11",
              url: "",
            },
            {
              id: 12,
              name: "管廊12",
              url: "",
            }
          ]
        },
        {
          id: 2,
          name: "管廊2",
          url: "",
        }
      ]
    }
  },
  data() {
    return {
      isCollapsed: false,
      screenWidth: 1200,
      screenHeight: 900,
      childValue: '',
      // img: BG,
    };
  },
  mounted() {
    this.screenWidth = document.body.clientWidth ;
    // let _this=this;
    // this.leftTree.filter(a => {
    //   if (a.url!=" ") {
    //     _this.goToMoudle({ path: a.url });
    //     return;
    //   }
    // })
  },
  computed: {
    menuitemClasses: function () {
      return ["menu-item", this.isCollapsed ? "collapsed-menu" : ""];
    }
  },
  methods: {
    goToMoudle(path) {
      this.$router.push(path);
      this.$emit('childByValue', path)
    },
  }
};
</script>

<style scoped>
.layout {
  background: #f5f7f9;
  position: relative;
  border-radius: 2px;
  overflow: hidden;
}

.coment{
  height:  calc(100vh - 65px);
}

.menu-item span {
  display: inline-block;
  overflow: hidden;
  width: 100px;
  text-overflow: ellipsis;
  white-space: nowrap;
  vertical-align: bottom;
  transition: width 0.2s ease 0.2s;
}

.menu-item i {
  transform: translateX(0px);
  transition: font-size 0.2s ease, transform 0.2s ease;
  vertical-align: middle;
  font-size: 16px;
}

.collapsed-menu span {
  width: 0px;
  transition: width 0.2s ease;
}

.collapsed-menu i {
  transform: translateX(5px);
  transition: font-size 0.2s ease 0.2s, transform 0.2s ease 0.2s;
  vertical-align: middle;
  font-size: 22px;
}

.layout-footer-center {
  text-align: center;
  background: none;
  color: #fff;
  background: -webkit-linear-gradient(left, #1c5d85, #286d96, #357aa1, #357aa1);
}

h1 {
  margin: 2% 1% 0 0;
  width: 8%;
  float: left;
}

.ivu-layout-sider >>> .ivu-layout-sider-trigger {
  background: #1c5d85;
}

.ivu-layout {
  background: #fff;
}

.ivu-layout-footer {
  padding: 13px 50px;
}

.ivu-menu-light {
  background: #1c5d85;
}

/* .ivu-menu-light.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu) {
  color: #00bcf5;
  z-index: 2;
  box-shadow: 2px 2px 20px #eee inset;
} */

.ivu-menu-submenu >>> .ivu-menu {
  color: #fff;
}

.ivu-menu-item:hover {
  color: #1d5f87 !important;
}

.ivu-menu {
  color: #fff;
}

li.ivu-menu-submenu-has-parent-submenu >>> .ivu-menu-submenu-title:hover,

.ivu-menu-light >>> .ivu-menu-submenu-title:hover {
  color: #1c5d85 !important;
}
</style>




