<template>
  <div>
    <ModulePage v-bind="operationLog"></ModulePage>
  </div>
</template>

<script>
import ModulePage from '../../../../components/Common/ModulePage'
export default {
  name: "UM_main",
  components:{ModulePage},
  data() {
    return {
        operationLog:{
          moduleName:"操作日志管理",
          leftTree:[]
        },
        treeNodeJumpUrl:"/UM/operationLog/query",
    };
  },
  created(){
    this.operationLog.leftTree = [];
    var _this=this;
    let temp={};
    temp.id=1;
    temp.name='列表';
    temp.url="/UM/operationLog/query";
    _this.operationLog.leftTree.push(temp);
  },
};
</script>


