<template>
    <div>
        <div class="conditions">
            <Row>
                <Col span="6">
                    <span class="conditionTitle">模块类型：</span>
                    <Input type="text" v-model="conditions.moduleType" placeholder="请输入模块类型" style="width: 60%" @on-keyup="queryOperationLog()"></Input>
                </Col>
                <Col span="6">
                    <span class="conditionTitle">操作人姓名：</span>
                    <Input type="text" v-model="conditions.reqUser" placeholder="请输入操作人姓名" style="width: 60%" @on-keyup="queryOperationLog()"></Input>
                </Col>
                <Col span="6">
                    <span class="conditionTitle">请求结果：</span>
                    <Select v-model="conditions.result"  style="width: 60%" @on-change="queryOperationLog()">
                        <Option value="null">所有</Option>
                        <Option v-for="item in result" :key="item.id" :value="item.name">{{item.name}}</Option>
                    </Select>
                </Col>
            </Row>
            <Row style="margin-top: 10px">
                <Col span="6">
                    <span class="conditionTitle">开始时间：</span>
                    <DatePicker type="datetime" placeholder="请选择开始时间" style="width: 60%" v-model="conditions.beginTime" @on-change="queryOperationLog()"></DatePicker>
                </Col>
                <Col span="6">
                    <span class="conditionTitle word25">结束时间：</span>
                    <DatePicker type="datetime" placeholder="请选择结束时间" style="width: 60%" v-model="conditions.endTime" @on-change="queryOperationLog()"></DatePicker>
                </Col>
            </Row>
        </div>
        <div>
            <Table :columns="columns1" :data="operationLog"></Table>
            <Page :total="page.pageTotal" :current="page.pageNum" :page-size="page.pageSize" show-sizer show-total   
                    placement="top" @on-change="handlePage" @on-page-size-change='handlePageSize' show-elevator style="text-align: right;margin-top: 20px;"></Page>
        </div>
    </div>
</template>
<script>
import axios from "axios";
export default {
    data(){
        return{
            conditions:{
                moduleType: null,
                reqUser: null,
                result: null,
                beginTime: null,
                endTime: null
            },
            page:{
                pageNum: 1,
                pageSize: 10,
                pageTotal: 0,
            },
            result:[
                {
                    id: 0,
                    name: '成功'
                },
                {
                    id: 1,
                    name: '失败'
                }
            ],
            operationLog:[],
            columns1: [
                {
                    type: 'index',
                    align: 'center',
                    width: 60
                },
                {
                    title: '模块类型',
                    key: 'moduleType',
                    align: 'center'
                },
                {
                    title: '请求IP',
                    key: 'reqIp',
                    align: 'center'
                },
                {
                    title: '请求人',
                    key: 'reqUser',
                    align: 'center'
                },
                {
                    title: '请求结果',
                    key: 'result',
                    align: 'center'
                },
                {
                    title: '操作描述',
                    key: 'desc',
                    align: 'center',
                    width: 400
                },
                {
                    title: '操作时间',
                    key: 'crtTime',
                    align: 'center'
                },
                {
                    title: '',
                    width: 150,
                    align: "center",
                    render: (h,params)=>{
                        return('div',[
                            h('Button',{
                                props: {
                                    type: "error",
                                    size: "small"
                                },
                                on: {
                                    click: () => {
                                        this.del(params.row.id);
                                    }
                                },
                            },'删除')
                        ])
                    }
                }
            ]    
        }
    },
    computed:{
        params() {
            let param = {
                pageNum: this.page.pageNum,
                pageSize: this.page.pageSize,
                moduleType: this.conditions.moduleType,
                reqUser: this.conditions.reqUser,
                beginTime: new Date(this.conditions.beginTime).getTime(),
                endTime: new Date(this.conditions.endTime).getTime(),
                // beginTime: this.conditions.beginTime,
                // endTime: this.conditions.endTime,
                result: this.conditions.result
            };
            return Object.assign({}, param);
        }
    },
    mounted(){
        this.queryOperationLog()
    },
    methods:{
        queryOperationLog(){
            axios.post('/operationlogs/datagrid',(this.params)).then(response=>{
                console.log(this.params)
                let{ code,data } = response.data
                if(code==200){   
                    if(data.list!=null){
                        for(let index in data.list){
                            data.list[index].crtTime =  new Date(data.list[index].crtTime).format('yyyy-MM-dd hh:mm:s')                      
                        }            
                        this.operationLog = data.list
                        this.page.pageTotal = data.total  
                    }
                }
            })
        },
        handlePage(value) {  
            this.page.pageNum = value 
            this.queryOperationLog()
        },  
        handlePageSize(value) {  
            this.page.pageSize = value  
            this.queryOperationLog()
        },
        del(id) {
            axios.delete("/operationlogs/" + id).then(response => {
                this.operationLog.splice(id, 1);
            });
            this.queryOperationLog()
        },
    }
}
</script>
<style scoped>
    .word25{
        letter-spacing: 0.25em;
        margin-right: -0.25em;
    }
    .conditions{
        margin-bottom: 20px;
    }   
</style>



