<template>
  <Form ref="addEnterGalleryApplication" :model="addEnterGalleryApplication" :label-width="120" :rules="ruleValidate" @submit.native.prevent>
    <FormItem label="申请人：">
      <Input v-model="addEnterGalleryApplication.staff.name" readonly></Input>
    </FormItem>
    <FormItem label="管廊：">
      <Input v-model="addEnterGalleryApplication.tunnel.name" readonly></Input>
    </FormItem>
    <FormItem label="入廊目的：">
      <Input v-model="addEnterGalleryApplication.actionName" readonly></Input>
    </FormItem>
    <FormItem label="计划入廊时间：">
      <DatePicker type="datetime" placeholder="请选择计划入廊时间" v-model="addEnterGalleryApplication.preTime" readonly style="width: 560px"></DatePicker>
    </FormItem>
    <FormItem label="进入管廊时间：">
      <DatePicker type="datetime" placeholder="请选择进入管廊时间" v-model="addEnterGalleryApplication.enterTime" readonly style="width: 560px"></DatePicker>
    </FormItem>
    <FormItem label="访客总人数：">
      <InputNumber  :min="1" v-model="addEnterGalleryApplication.visitorNumber" style="width: 560px;"></InputNumber>
    </FormItem>
    <FormItem label="访客所属公司">
      <Input v-model="addEnterGalleryApplication.company.name" readonly></Input>
    </FormItem>
    <FormItem label="访客详细信息：">
      <Table border :columns="columns1" :data="addEnterGalleryApplication.visitorInfo"></Table>
    </FormItem>
    <FormItem label="备注：" v-show="examineStatus==1||examineStatus==2">
      <Input type="textarea" v-model="addEnterGalleryApplication.remark" :autosize="{minRows: 2,maxRows: 5}" readonly></Input>
    </FormItem>
    <FormItem label="审批意见：">
      <Button type="success" v-show="examineStatus==1||examineStatus==4" class="btn">同意</Button>
      <Button type="error" v-show="examineStatus==2||examineStatus==4" class="btn">不同意</Button>
      <Button type="info" v-show="examineStatus==3" class="btn">审核中</Button>
    </FormItem>
    <FormItem label="出廊时间：" v-show="examineStatus==1" prop="exitTime">
      <DatePicker type="datetime" placeholder="请选择出廊时间" v-model="addEnterGalleryApplication.exitTime" style="width: 560px"></DatePicker>
    </FormItem>
    <FormItem style="text-align: center" v-show="examineStatus==1">
      <Button type="primary"  @click="submitExitTime('addEnterGalleryApplication')">提交</Button>
      <Button type="default">取消</Button>
    </FormItem>  
  </Form>
</template>
<script>
  import axios from "axios";
  import types from '../../../../../static/Enum.json'
  export default {
    data(){
      return{
        addEnterGalleryApplication:{
          id: null,
          staff:{
            name: null
          },
          tunnel:{
            name: null            
          },
          actionName: null,
          preTime:  null,
          enterTime: null,
          visitorNumber: null,
          company: {
            name: null
          },
          positionId: null,
          visitorInfo:[] ,
          remark: null,
          processDefinitionId: null,
          statusName: null,
          exitTime: null
        },
        columns1: [
          {
            title: '姓名',
            key: 'name',
            align: 'center'
          },
          {
            title: '身份证号',
            key: 'idCard',
            align: 'center'
          },
          {
            title: '联系方式',
            key: 'tel',
            align: 'center'
          }
        ],
        ruleValidate:{
          exitTime:[
              {required: true, type: 'date', message: '请选择出廊时间', trigger: 'change'}
          ],
        },
        examineStatus: 1,
        examine: types.examineStatus,
      }
    },
    computed:{
      params() {
        let param = {
          remark: this.addEnterGalleryApplication.remark,
          id: this.addEnterGalleryApplication.id,
          processDefinitionId: this.addEnterGalleryApplication.processDefinitionId,
          value: 'agree',
          exitTime: new Date(this.addEnterGalleryApplication.exitTime).getTime()
        }
        return Object.assign({}, param);
      }
    },
    mounted(){
      this.examineStatus = this.$route.params.type
      axios.get('/users/activiti/task/detail//'+this.$route.params.processDefinitionId+'/'+this.$route.params.processType).then(response=>{
        let{ code,data } = response.data
            if(code=200){
                this.addEnterGalleryApplication = data
                var arr = new Array()
                for(let index in response.data.data.visitorInfo.split(',')){
                    var str = response.data.data.visitorInfo.split(',')[index]
                    arr.push(str)
                }
                var arr2 = new Array()
                for(let k in arr){
                  var obj = {
                    name: arr[k].split('-')[0],
                    idCard: arr[k].split('-')[1],
                    tel: arr[k].split("-")[2]
                  }
                }
                arr2.push(obj)
                this.addEnterGalleryApplication.visitorInfo = arr2
                this.addEnterGalleryApplication.preTime = new Date(this.addEnterGalleryApplication.preTime).format('yyyy-MM-dd hh:mm:s')
                this.addEnterGalleryApplication.enterTime = new Date(this.addEnterGalleryApplication.enterTime).format('yyyy-MM-dd hh:mm:s')                
            }   
      })
    },
    methods:{
      submitExitTime(name){
        this.$refs[name].validate((valid) => {
          if(valid){
            axios.post('users/activiti/req-historys/audit',(this.params)).then(response=>{
              let{ code,data } = response.data
              if(code==200){
                console.log(response.data)
                this.$router.push('/UM/myApplication/query')
              }
            })
          }else{
            this.$Message.error('请输入正确的申请信息');
          }
        })
      }
    }
  }
</script>
<style scoped>
  .ivu-form.ivu-form-label-right{
    width: 680px;
    margin: 20px auto;
  }
  ul li{
    list-style: none;
  }
  button{
    width: 80px;
  }
  .btn{
    cursor: text;
  }
</style>




