<template>
    <div>
        <h1>管廊设备信息总览</h1>
        <Row>
            <Col span="18">
                <Col span="6">
                    <data-box v-bind="equimentTotalNum"></data-box>
                </Col>
                <Col span="6">
                    <data-box v-bind="equimentRunNum"></data-box>
                </Col>
                <Col span="6">
                    <data-box v-bind="spareAFew"></data-box>
                </Col>
                <Col span="6">
                    <data-box v-bind="duration"></data-box>
                </Col>
                <Col span="24">
                    <div class="GISbox">
                        <!--<h3>地图显示所有的管廊位置GIS</h3>-->
                        <!-- <canvas :id="CanvasData.id"></canvas> -->
                      <sm-viewer
                        :id="id"
                        @onload="onload">
                      </sm-viewer>
                        <div class="tunnelProfile">
                            <div>设备信息概况</div>
                            <div>设备数：100</div>
                            <div>管廊1：70</div>
                            <div>管廊2：30</div>
                            <div>故障数：5</div>
                        </div>
                    </div>
                </Col>
            </Col>
            <Col span="6" class="chartBox">
                <Col span="24" style="height: 49%;">
                    <!-- <Button class="equimentChartBtn" @click.native="goToMoudle({ path:'/UM/equipment/query'})">更多</Button> -->
                    <div style="width: 21.4vw; height:100%;" class="equipmentChartBox">
                        <simple-bar v-bind="equipmentChart"></simple-bar>
                    </div>
                </Col>
                <Col span="24" style="height: 49%;">
                    <!-- <Button class="equimentChartBtn" @click.native="goToMoudle({ path:'/UM/equipment/query'})">更多</Button> -->
                    <div style="width: 21.4vw; height:100%;" class="equipmentChartDoubleColor">
                        <simple-pie v-bind="equipmentChartDoubleColor"></simple-pie>
                    </div>
                </Col>
            </Col>
        </Row>
        <Row style="margin-top:2vh;">
          <Col span="5">
              <div class="circleBox" style="background: #b189a357">
                  <process-ring v-bind="spare">
                      <p>{{spare.explain}}</p>
                      <p>{{spare.percent}}%</p>
                  </process-ring>
              </div>
          </Col>
          <Col span="5">
              <div class="circleBox" style="background: #6e6ec761">
                  <process-ring v-bind="breakDown">
                      <p>{{breakDown.explain}}</p>
                      <p>{{breakDown.percent}}%</p>
                  </process-ring>
              </div>
          </Col>
          <Col span="7" style="margin-top: 5px">
              <Card style="width: 98%">
                  <p slot="title">故障信息列表</p>
                  <p v-for="(item,index) in faultInfoList" value="item" :key="index">{{item.list}}</p>
              </Card>
          </Col>
          <Col span="7" style="margin-top: 5px">
              <Card style="width: 98%">
                  <p slot="title">管廊设备信息</p>
                  <p v-for="(item,index) in faultInfoList" value="item" :key="index">{{item.list}}</p>
              </Card>
          </Col>
        </Row>
    </div>
</template>

<script>
import DataBox from "../../../../components/Common/Box/DataBox";
import SimpleBar from "../../../../components/Common/Chart/SimpleBarChart";
import SimplePie from "../../../../components/Common/Chart/SimplePieChart";
import SmViewer from "../../../../components/Common/3DViewer";
import ProcessRing from "../../../../components/Common/ProcessRing";
import { URL_CONFIG } from "../../../../../static/3DMap/js/3DMapConfig";
import { setViewAngle } from "../../../../scripts/commonFun";
import equimentTotalNum from "../../../../assets/UM/equimentTotalNum.png";
import spareAFew from "../../../../assets/UM/spareAFew.png";
import duration from "../../../../assets/UM/duration.png";
import equimentRunNum from "../../../../assets/UM/equimentRunNum.png";

export default {
  name: "equipmentMain",
  data() {
    return {
      id: "GIS_ID",
      camera: {
        longitude: 116.43709465365579,
        latitude: 39.91793569836651,
        height: 1245.5482069457155,
        roll: 6.281841926729911,
        pitch: -0.9120242487858476,
        heading: 1.5735504792701676
      },
      equimentTotalNum: {
        label: "设备总数",
        value: 100,
        imgSrc: equimentTotalNum,
        imgBac: "#CCCCFF",
        valBackground: "#e4bdeb",
        labelBackground: "#CC99CC"
      },
      equimentRunNum: {
        label: "运行中的设备数",
        value: 87,
        imgSrc: equimentRunNum,
        imgBac: "#999999",
        valBackground: "#cccccc",
        labelBackground: "#336666"
      },
      spareAFew: {
        label: "备品数",
        value: "5",
        imgSrc: spareAFew,
        imgBac: "#466b91",
        valBackground: "#98ccc2",
        labelBackground: "#b6a8db"
      },
      duration: {
        label: "设备运行总时长",
        value: "158h",
        imgSrc: duration,
        imgBac: "#aaccd2",
        valBackground: "#eaba0a",
        labelBackground: "#554d6b"
      },
      equipmentChart: {
        id: "equipmentChartId",
        requestUrl: "/equipments/type/count",
        title: "每月每条管廊的任务数"
      },
      equipmentChartDoubleColor: {
        id: "equipmentCharDoubleColortId",
        requestUrl: "/equipments/type/count",
        title: {
          text: "巡检任务正常比例",
          x: "left",
          textStyle: {
            color: "#2e739b"
          }
        }
      },
      equimentPie: {
        id: "equipmentPieId",
        requestUrl: "/equipments/type/count",
        title: "各类型故障占比"
      },
      spare: {
        id: "spare",
        explain: "备品占比",
        percent: 85,
        strokeWidth: 10,
        radius: 60
      },
      breakDown: {
        id: "breakDown",
        explain: "故障占比",
        percent: 45,
        strokeWidth: 10,
        radius: 60
      },
      faultInfoList: [
        { list: "设备1故障信息" },
        { list: "设备10故障信息" },
        { list: "设备1故障信息" },
        { list: "设备10故障信息" },
        { list: "设备1故障信息" },
        { list: "设备10故障信息" },
        { list: "设备1故障信息" },
        { list: "设备10故障信息" },
        { list: "设备1故障信息" },
        { list: "设备10故障信息" },
        { list: "设备1故障信息" },
        { list: "设备10故障信息" }
      ],
      tunnelInfoList: [
        { list: "管廊1设备总数70，损坏3" },
        { list: "管廊1设备总数70，损坏3" },
        { list: "管廊1设备总数70，损坏3" },
        { list: "管廊1设备总数70，损坏3" },
        { list: "管廊1设备总数70，损坏3" },
        { list: "管廊1设备总数70，损坏3" }
      ]
    };
  },
  components: {
    DataBox,
    SimpleBar,
    SimplePie,
    ProcessRing,
    SmViewer
  },
  mounted() {},
  methods: {
    goToMoudle: function(path) {
      this.$router.push(path);
    },
    onload(parent) {
      let Cesium = parent.Cesium;
      let _this = this;
      // 初始化viewer部件
      var viewer = new Cesium.Viewer(this.id, {
        navigation: false //关闭导航控件
      });
      var scene = viewer.scene,
        widget = viewer.cesiumWidget,
        imageryLayers = viewer.imageryLayers,
        imagery_mec;

      var provider_mec = new Cesium.SuperMapImageryProvider({
        url: URL_CONFIG.IMG_MAP //墨卡托投影地图服务
      });
      imagery_mec = imageryLayers.addImageryProvider(provider_mec);

      try {
        //打开所发布三维服务下的所有图层
        var promise = scene.open(URL_CONFIG.BIM_SCP);
        Cesium.when(
          promise,
          function(layer) {
            var overGroundLayer = scene.layers._layerQueue[0];
            //设置相机位置、视角，便于观察场景
            setViewAngle(scene, Cesium, _this.camera);
          },
          function(e) {
            if (widget._showRenderLoopErrors) {
              var title =
                "加载SCP失败，请检查网络连接状态或者url地址是否正确？";
              widget.showErrorPanel(title, undefined, e);
            }
          }
        );
      } catch (e) {
        if (widget._showRenderLoopErrors) {
          var title = "渲染时发生错误，已停止渲染。";
          widget.showErrorPanel(title, undefined, e);
        }
      }
    }
  }
};
</script>
<style scoped>
h1,
.ivu-card >>> .ivu-card-head p {
  color: #2e739b;
}
.tunnelProfile {
  border: 1px solid #dddee1;
  border-radius: 4px;
  position: absolute;
  bottom: 2px;
  right: 13px;
}
.tunnelProfile div {
  border: 1px solid #dddee1;
  border-bottom: none;
  line-height: 35px;
  text-align: center;
  padding-left: 10px;
  padding-right: 10px;
}
.chartBox {
  height: 100%;
  position: absolute;
  right: 0px;
  margin-right: 10px;
}
.chartBox .ivu-col-span-24 {
  margin-bottom: 10px;
}
.equipmentChartBox,
.equipmentChartDoubleColor {
  border: 1px solid #dddee1;
  border-radius: 4px;
  margin: 10px;
  margin-top: 0px;
}
.GISbox {
  width: 99%;
  height: 45vh;
  border: 1px solid #dddee1;
  border-radius: 4px;
  margin-top: 20px;
}
ul {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding-left: 5px;
  height: 80px;
  overflow: auto;
}
ul li {
  list-style: none;
  line-height: 20px;
}
.ivu-col-span-12 {
  padding: 10px;
}
.circleBox {
  text-align: center;
  height: 18vh;
  width: 98%;
  display: inline-block;
  vertical-align: middle;
  border: 1px solid #dddee1;
  border-radius: 4px;
  margin-top: 5px;
  padding-top: 10px;
}
.ivu-card >>> .ivu-card-body {
  height: 12.5vh;
  overflow-y: auto;
}
</style>






