<template>
    <div style="padding-left: 10px;">
        <Row class="top">
            <Col span="6">
                <span class="planDec">设备类型：</span>
                <Select v-model="condition.type"  @on-change='showTable()'  style="width:60%">
                    <Option value=null key="0">所有</Option>
                    <Option v-for="item in equipmentTypes" :value="item.key" :key="item.key">{{ item.value }}</Option>
                </Select>
            </Col>
            <Col span="6">
                <span class="planDec">设备状态：</span>
                <Select v-model="condition.status"  @on-change='showTable()'  style="width:60%">
                    <Option value=null key="0">所有</Option>
                    <Option v-for="item in equipmentStatus" :value="item.key" :key="item.key">{{ item.value }}</Option>
                </Select>
            </Col>
            <Col span="6">
                <span class="planDec">设备启用时间：</span>
                <DatePicker type="date" placeholder="Select date" style="width: 120px" v-model="condition.startEquipmentTime" @on-change='showTable()'></DatePicker>~
                <DatePicker type="date" placeholder="Select date" style="width: 120px" v-model="condition.endEquipmentTime" @on-change='showTable()'></DatePicker>
            </Col>
            <Col span="6">
                <span class="planDec">设备维修时间：</span>
                <DatePicker type="datetimerange" placeholder="请选择设备维修时间" style="width: 280px"></DatePicker>
            </Col>
        </Row>
        <Row class="bottom">
            <Col span="5" class="equipmentList">设备列表</Col>
            <Col span="19" align="right">
                <Input style="width: 300px;" v-model="condition.fuzzy">
                    <Button slot="append" icon="ios-search"></Button>
                </Input>
            </Col>
            <Col span="24">
                <div class="addList" @click="add({path: '/UM/equipment/add'})">
                    <Icon type="plus-round"></Icon>
                    添加
                </div>
            </Col>
            <ul class="table">
                <li v-for="(item,index) in equipments" :key="item.index">
                    <div class="inline-box">
                        <img :src="item.imgUrl">
                    </div>
                    <div class="inline-box" style="width: 30%">
                        <div class="tunnelName">{{ item.name }}</div>
                        <div>{{ item.statusName }}</div>
                    </div>
                    <div class="inline-box" style="width: 25%;text-align: right;padding-right: 20px;">
                        <div>{{ item.tunnel.name }}</div>
                        <div>{{ item.typeName }}</div>
                    </div>
                    <div class="inline-box" style="width: 25%">
                        <div>启用时间</div>
                        <div>{{ item.crtTime }}</div>
                    </div>
                    <div class="inline-box options" style="width: 10%">
                        <span class="edit" @click="edit(index)">编辑</span>
                        <span>|</span>
                        <span @click="show(index)">查看</span>
                        <span>|</span>
                        <span @click="del(index)">删除</span>
                    </div>
                </li>
            </ul>
            <div class="paging" v-cloak>
                 <Page :total="page.pageTotal" :current="page.pageNum" :page-size="page.pageSize" show-sizer show-total
                    placement="top" @on-change="handlePage" @on-page-size-change='handlePageSize' show-elevator></Page>
            </div>
        </Row>
    </div>
</template>

<script>
import axios from "axios";
import types from '../../../../../static/Enum.json'
export default {
    name:"queryEquipment",
    data(){
        return {
            showOn: true,
            equipments:[],
            tunnels: [],
            tunnelId: 1,
            equipmentTypes:[],
            condition:{
                tunnel:{
                    name: '',
                },
                type:null,
                status: null,
                fuzzy: null,
                startEquipmentTime: null,
                endEquipmentTime: null
            },
            equipmentStatus: types.equipmentStatus,
            pagingList:[],
            page: {
                pageNum: 1,
                pageSize: 10,
                pageTotal: 0,
            },
        }
    },
    watch: {
        '$route': function () {
        //2. $route发生变化时再次赋值planId
        this.tunnelId = this.$route.params.id;
        this.tunnels.forEach(a => {
            if (a.id == this.tunnelId) {
            this.showTable();
            }
        });
        }
    },
    computed: {
        params() {
            let param = {
                pageNum: this.page.pageNum,
                pageSize: this.page.pageSize,
                tunnelId: this.tunnelId,
                type: this.condition.type,
                status: this.condition.status,
                startEquipmentTime: new Date(this.condition.startEquipmentTime).getTime(),
                endEquipmentTime: new Date(this.condition.endEquipmentTime).getTime()
            }
            return Object.assign({}, param);
        },
    },
    mounted(){
        //从数据库读取select的option选项
        axios.get("/tunnels ").then(response =>{
            let { code, data } = response.data;
            if (code == 200) {
                this.tunnels = data;
                this.selectedTunnel = this.tunnels.length > 0 ? this.tunnels[0] : {};
                this.selectedTunnelName = this.selectedTunnel.name;
            }
        });
        this.equipmentTypes = types.equipmentType;
        this.showTable()
    },
    methods: {
        // type 1:查看， 2：编辑
        goToMoudle: function (index, type) {
            this.$router.push({
                name: 'UMDetailEquipment',
                params: {
                    id: this.equipments[index].id,
                    type: type,
                }
            });
        },
        showTable(){
            axios.post("/equipments/datagrid", (this.params)).then(result=>{
                let {code, data} = result.data;
                if (code == 200){
                     for (let index in data.list) {
                      data.list[index].crtTime = new Date(data.list[index].crtTime).format("yyyy-MM-dd");
                      if(data.list[index].imgUrl!=null){
                        data.list[index].imgUrl = "http://192.168.0.41:8080/MaxTunnel-Web/"+data.list[index].imgUrl.replace(/\\/g,"/")
                      }
                     }
                    this.equipments = data.list;
                    this.page.pageTotal = data.total;
                }
            });
        },
        handlePage(value) {
            this.page.pageNum = value
            this.showTable()
        },
        handlePageSize(value) {
            this.page.pageSize = value
            this.showTable()
        },
        show(index){
            this.goToMoudle(index, types.pageType.Read);
        },
        edit(index){
            this.goToMoudle(index, types.pageType.Edit);
        },
        add(path) {
            this.$router.push(path);
        },
        pickUp(){
            this.showOn = !this.showOn;
        }
    }
}
</script>
<style scoped>
    .top{
        margin-bottom: 20px;
    }
    .planDec{
        margin-right: 5px;
    }
    .bottom{
        margin-top: 40px;
    }
    .equipmentList{
        font-size: 17px;
        font-weight: 700;
    }
    .addList{
        border: 1px dashed #dfdfdf;
        text-align: center;
        line-height: 40px;
        color: #8b8b8b;
        margin-top: 20px;
        border-radius: 10px;
    }
    .equipmentTable{
        margin-top: 10px;
        border-color: #eeeeee;
    }
    ul.table,li{
        width: 100%;
    }
    .table li{
        list-style: none;
        border-bottom: 1px solid #c5c2c2;
        padding: 5px;

    }
    .table li .inline-box{
        display: inline-block;
        vertical-align: middle;
        color: #8b8b8b;
    }
    .table li .inline-box div{
        line-height: 30px;
    }
    .inline-box img{
        width: 70px;
        height: 50px;
        background: #e8e8e8;
        margin-top: 10px;
        margin-right: 20px;
    }
    .tunnelName{
        font-size: 14px;
        font-weight: 700;
        color: black;
    }
    .options span{
        color: cornflowerblue;
        padding: 5px;
        cursor:pointer;
    }
    .paging{
        margin-top: 15px;
        text-align: right;
    }
</style>





