<template>
    <div>
       <Row class="conditions">
            <Col span="10">
            监测仓:
            <Select style="width:20vw;" v-model="query.storeId">
              <Option value="">全部</Option>
              <Option v-for="item in stores" :value="item.id" :key="item.name">{{ item.name }}
              </Option>
            </Select>
            </Col>
            <Col span="10">
            区域:
            <Select style="width:20vw;" v-model="query.areaId">
              <Option value="">全部</Option>
              <Option v-for="item in areas" :value="item.id" :key="item.name">{{ item.name }}</Option>
            </Select>
            </Col>
            <!-- <Col span="6">
            区段:
            <Select style="width:12vw;" v-model="query.sectionId">
              <Option v-for="item in sections" :value="item.id" :key="item.name">{{ item.name }}</Option>
            </Select>
            </Col> -->
            <Col span="3">
                <Button type="primary" shape="circle" icon="ios-search" @click="search"></Button>
            </Col>
        </Row>
        <div class="content">
         <Row class="sectionsInfo">
            <Col span="6" class="card" v-for="(cab,index) in cables" :key="index">
                <div class="title">
                    <span>{{cab.name}}</span>
                </div>
                <div class="linesInfo">
                    <p v-for="(line,i) in cab.value" :key="i" :class="[{'green' : line.key === '可用管线数'},{'red':line.key === '已用管线数'}]">{{ line.key }}:{{ line.val }}</p>
                </div>
                <div class="option">
                    <Button size=small type="primary" @click="details(index)">详情</Button>
                    <!-- <Button size=small type="error">删除</Button> -->
                </div>    
            </Col>
        </Row> 
        </div>
        <Page :total="page.pageTotal" :current="page.pageNum" :page-size="page.pageSize" show-sizer show-total   
                    placement="top" @on-change="handlePage" @on-page-size-change='handlePageSize' show-elevator style="text-align: right;margin-top: 20px;"></Page> 
    </div>
</template>
<script>
import axios from "axios";
import Enum from "../../../../../static/Enum.json";
import Vue from 'vue';
export default {
    data(){
        return{
           tunnelId:'',
           stores:[],
           areas:[],
           sections:[],
           query:{
            storeId:'',
            areaId:'',
            sectionId:''
           },
           page:{
                pageNum: 1,
                pageSize: 10,
                pageTotal: null,
            },
            ids:[],
            cables:[]
        }
    },
    mounted() {
      this.tunnelId = this.$route.params.id;
      // console.log(this.tunnelId);
      this.initData();
    },
    watch: {
      '$route': function () {
        // $route发生变化时再次赋值planId
        this.tunnelId = this.$route.params.id;
        this.initData();
        }
    },
    methods:{
        initData() {
            this.axios.get('tunnels/' + this.tunnelId + '/stores').then(response => {
              let {code, data} = response.data;
              if (code == 200) {
                this.stores = data;
                this.query.storeId = data[0].id;
                let params = {
                    storeId: this.query.storeId
                };
                this.axios.post('tunnels/stores/areas/sections/condition',params).then(response =>{
                    let {code,data} = response.data;
                    if(code == 200){
                        this.sections = data;
                    }
                })
              }
            })
            this.axios.get('tunnels/'+ this.tunnelId + '/areas').then(response =>{
                let {code,data} = response.data;
                if(code == 200){
                    this.areas = data;
                }
            })
            
        },
        // changeQuery() {
        //     let params = {
        //          storeId: this.query.storeId,
        //          areaId: this.query.areaId
        //     };
        //     this.axios.post('tunnels/stores/areas/sections/condition',params).then(response =>{
        //         let {code,data} = response.data;
        //         if(code == 200){
        //             this.sections = data;
        //         }
        //     }) 
        // },
        search(){
            if(!this.query.storeId && !this.query.areaId){
               this.$Message.error("请至少选择一个监测仓或区域");
            }
            let params = {
                storeId: this.query.storeId,
                areaId: this.query.areaId,
                id: this.query.sectionId,
                pageNum: this.page.pageNum,
                pageSize: this.page.pageSize,
                name:''
            };
            this.axios.post('sections/datagrid',params).then(response =>{
                let {code,data} = response.data;
                this.page.pageTotal = data.total;
                let _this = this;
                console.log(data.list);
                if(code == 200){
                    _this.cables = [];
                    _this.ids = [];
                    data.list.forEach(a=>{
                         _this.ids.push(a.id);
                         let temp={};
                         temp.name=a.store.name + a.area.name;
                         temp.id=a.id;
                         temp.value=null;
                         _this.cables.push(temp);
                    })
                    this.axios.get('tunnels/areas/sections/batch/' + _this.ids+ '/cables-count').then(response =>{
                        let {code,data} = response.data;
                        if(code == 200){
                            data.forEach(a=>{
                                _this.cables.forEach(b=>{
                                    if(b.id==a.id){
                                        b.value=a.val;
                                    }
                                })
                            })
                              
                        }
                    })
                }
            })
            // console.log(this.cables);
        },
         handlePage(value) {
            this.page.pageNum = value;
            this.search();
        },
        handlePageSize(value) {
            this.page.pageSize = value;
            this.search();
        },
        details(index){
             this.$router.push({
               path: '/UM/OperatingSpace/details/' + this.cables[index].id
            });
        }
    }
}
</script>
<style scoped>
.conditions {
    margin: 10px;
  }
.content{
    width:96%;
    margin-left:2%;
}
.sectionsInfo{
    margin-top: 20px;
    /* text-align: center; */
}
.card{
    border: 1px solid#dddfe1;
    width: 260px;
    margin: 10px 20px;
    padding: 5px 0px;
    border-radius: 4px;
    box-shadow: 5px 6px 4px rgba(0, 0, 0, .2);
}
 .title{
    padding-left: 10px;
    font-size: 18px;
}
.linesInfo{
    text-align: center;
    font-size:16px;
    margin:10px auto;
}
.option{
    float:right;
    padding: 0px 10px 0px 0px;
}
.green{
    color:rgb(25,100,107);
}
.red{
    color:rgb(225,102,0);
}
</style>



