<template>
  <ModulePage v-bind="curModule"></ModulePage>
</template>

<script>
  import ModulePage from '../../../../components/Common/ModulePage'

  export default {
    name: "UM_main",
    data() {
      return {
        curModule: {
          moduleName: "管廊本体监控",
          leftTree: []
        },
        treeNodeJumpUrl: "/UM/TunnelSupervise/list/",
      }
    },
    components: {
      ModulePage
    },
    created() {
      this.curModule.leftTree = [];
      this.axios.get("/tunnels").then(result => {
        let {code, data} = result.data;
        var _this = this;
        if (code == 200) {
          data.forEach(a => {
            let temp = {};
            temp.id = a.id;
            temp.name = a.name;
            temp.url = _this.treeNodeJumpUrl + a.id;
            _this.curModule.leftTree.push(temp);
          })
          this.goToMoudle({path: this.curModule.leftTree[0].url});
        }
      });

    },
    mounted() {
    },
    methods: {
      goToMoudle(path) {
        this.$router.push(path);
      }
    }
  };
</script>




