<template>
  <div class="Map">
      <div class=mapTop>
          <div style="width: 70%; height: 100%; padding-left: 14%;position: absolute;left: 6%">
          <row type="flex" justify="center" align="bottom">
            <Col span="1">
            <Dropdown divided @click.native="homeSwitch">
              <a> <img src="../../../assets/VM/home.png" height="40%" width="100%"></a>
            </Dropdown>
            </Col>
            <Col span="1" offset="1">
            <Dropdown divided @click.native="logout">
              <a> <img src="../../../assets/VM/user.png" height="40%" width="100%"></a>
            </Dropdown>
            </Col>
            <Col span="1" offset="1">
            <Dropdown divided @click.native="VideoSwitch">
              <a> <img src="../../../assets/VM/video.png" height="40%" width="100%"></a>
            </Dropdown>
            </Col>
            <Col span="1" offset="1">
            <Dropdown divided @click.native="gesSwitch">
              <a> <img src="../../../assets/VM/genue.png" height="40%" width="100%"></a>
            </Dropdown>
            </Col>
            <Col span="1" offset="1">
            <Dropdown divided @click.native="logout">
              <a> <img src="../../../assets/VM/roboto.png" height="40%" width="100%"></a>
            </Dropdown>
            </Col>
            <Col span="1" offset="1">
            <Dropdown divided @click.native="logout">
              <a> <img src="../../../assets/VM/key.png" height="40%" width="100%"></a>
            </Dropdown>
            </Col>
            <Col span="1" offset="1">
            <Dropdown divided @click.native="logout">
              <a> <img src="../../../assets/VM/stop.png" height="15%" width="80%"></a>
            </Dropdown>
            </Col>
            <Col span="1" offset="1">
            <Dropdown divided @click.native="logout">
              <a> <img src="../../../assets/VM/fire.png" height="15%" width="80%"></a>
            </Dropdown>
            </Col>
            <Col span="1" offset="1">
            <Dropdown divided @click.native="alermUnitSwitch">
              <a> <img src="../../../assets/VM/mobile.png" height="15%" width="80%"></a>
            </Dropdown>
            </Col>
          </row>
          <row justify="center" type="flex">
            <img src="../../../assets/VM/footLine.png" height="8px" width="85%">
          </row>
        </div>
      </div>
      <temperature class=maptemperature></temperature>
      <div class=mapRight>
          <!--<temperature class=maptemperature></temperature>-->
              <transition
                  :enter-active-class="enterClass"
                  :leave-active-class="leaveClass">
              <map-gauges v-if="show.ges"></map-gauges>
          </transition>
      </div>
      <transition
              :enter-active-class="enterClass"
              :leave-active-class="leaveClass">
          <warn-unit
                  v-bind="alermUnitStyle"
                  v-if="show.alermUnit">
          </warn-unit>
      </transition>
      <sm-viewer
      :id="id"
      @onload="onload">
    </sm-viewer>
  </div>
</template>
<style scoped>
  .mapTop {
    z-index: 1001;
    position: absolute;
    width: 97.3%;
    height: 7%;

  }
  .mapRight {
    z-index: 1002;
    position: absolute;
    /*margin-left: 87%;*/
    width: 8%;
    height: 68%;
    right: 2%;
  }
  .maptemperature{
    /*margin-top: 10%;*/
  }
  .cesium-viewer-bottom{
    display:none
  }
  .mapRight >>> + .ivu-table-wrapper{
      position: absolute;
      z-index: 1002;
      top: 16%;
      right: 2%;
      /*!*box-shadow: 0 0 1rem rgba(0,0,0,.5);*!  边框样式*/
      /*background: rgba(0,0,0,.3);*/
    /*边框样式,下面为添加样式*/
    border-image: url(http://www.imaoda.com/s/img/tpl/border.png) 1;
    background: rgba(0,26,39,.3);
    box-shadow: 0 0 1.5rem rgba(193,255,255,1) inset;
    border-bottom: 1;
    border-right: 1;
  }
  /*边框样式*/
 .mapRight >>> .ivu-table:before{
   width: 0;
 }
  .mapRight >>> .ivu-table:after{
    width: 0;
  }
</style>
<style>
    .cesium-infoBox{
        background: rgba(3,19,36,0.9);
        box-shadow: 0 0 10px 1px rgba(3,19,36,0.9);
        top:16%;
    }
    .cesium-infoBox-title {
        background: rgba(3,19,36,1);
    }
</style>

<script>
  import SmViewer from '../../Common/3DViewer'
  import Temperature from '../VMBodyCenter/temperatureBox'
  import warnUnit from './warnUnit'
  import MapGauges from './gauges'
  import $ from 'jquery'
  import { URL_CONFIG } from '../../../../static/3DMap/js/3DMapConfig'
  import { setViewAngle ,doSqlQuery } from '../../../scripts/commonFun'
  import  { VLC } from '../../../../static/VM/js/VMGlobalConfig'


  export default {
    data() {
      return {
          msg: '地图',
          id: 'cesiumContainer',
          show:{
              ges:false,
              alermUnit:false
          },
          enterClass:'animated bounceInDown',
          leaveClass:'animated zoomOut',
          alermUnitStyle:{
              width:0,
              height:0
          },
          camera:{
              longitude:116.43709465365579,
              latitude:39.91793569836651,
              height:1245.5482069457155,
              roll:6.281841926729911,
              pitch:-0.9120242487858476,
              heading:1.5735504792701676
          },
          viewer:null,
          allVideos:[]//
      }
    },
    components: {
        SmViewer,
        Temperature,
        warnUnit,
        MapGauges
    },
    watch:{
        allVideos(){
            // this.allVideos
            this.addEntityObj(this.allVideos)
        }
    },
    mounted() {
        this.init();
    },
    methods: {
        init() {
            let _this=this;

            _this.calculateStyle();
            _this.getVideos();
        },
        onload(parent) {
            let Cesium = parent.Cesium,
                _this=this;

            // 初始化viewer部件
            var viewer = new Cesium.Viewer(this.id,{
                navigation:false, //关闭导航控件
                infoBox:false //禁用信息框
            });

            _this.viewer=viewer;
            var scene = viewer.scene,
                 widget = viewer.cesiumWidget,
                 imageryLayers = viewer.imageryLayers,
                 imagery_mec;
            // var provider_mec = new Cesium.SuperMapImageryProvider({
            //     url : URL_CONFIG.IMG_MAP//墨卡托投影地图服务
            // });
            //  imagery_mec = imageryLayers.addImageryProvider(provider_mec);

            try{
              //打开所发布三维服务下的所有图层
              var promise = scene.open(URL_CONFIG.BIM_SCP);
              Cesium.when(promise,function(layer){

                  var overGroundLayer = scene.layers._layerQueue[0];
                //设置相机位置、视角，便于观察场景
                  setViewAngle(scene,Cesium,_this.camera)

                  layer.forEach(element => {
                      // 结构性框架的要查数据，非结构性框架的不用管了，也不能选择
                      if (element.name.indexOf("结构框架") < 0) {
                          element.selectEnabled = false;
                      } else {
                          // 设置查找参数
                          element.setQueryParameter({
                              url: URL_CONFIG.BIM_DATA,
                              dataSourceName: "tunnel",
                              dataSetName: "结构框架",
                              keyWord: "SmID"
                          });

                      }
                  });

                  viewer.pickEvent.addEventListener(function(feature) {
                      console.log("feature", feature);
                  });
                  // _this.doSqlQuery(viewer);

              },function(e){
                if (widget._showRenderLoopErrors) {
                  var title = '加载SCP失败，请检查网络连接状态或者url地址是否正确？';
                  widget.showErrorPanel(title, undefined, e);
                }
              });
            }
            catch(e){
              if (widget._showRenderLoopErrors) {
                var title = '渲染时发生错误，已停止渲染。';
                widget.showErrorPanel(title, undefined, e);
              }
            }
            //设置鼠标左键单击回调事件
            viewer.selectedEntityChanged.addEventListener(feater=>{
                if(feater != undefined){
                    _this.replaceVideoUrl(feater._id)
                }
            })
      },
        gesSwitch(){
            let _this=this;

            _this.show.ges=!_this.show.ges;
            _this.show.alermUnit=false;
        },
        alermUnitSwitch(){
            let _this=this;

            _this.show.alermUnit=!_this.show.alermUnit;
            _this.show.ges=false;
        },
        homeSwitch(){
            let _this=this;

            _this.show.ges=_this.show.alermUnit=false;
        },
        calculateStyle(){

            let alermUnitHeight= parseFloat($('.Map').css('height')),
                alermUnitWidth= parseFloat($('.Map').css('width'));

            this.alermUnitStyle.height=alermUnitHeight-alermUnitHeight*0.2*2;
            this.alermUnitStyle.width=alermUnitWidth*0.48;
        },
        onQueryComplete(viewer){
            let _this=this;

            return function(queryEventArgs){
                var selectedFeatures = queryEventArgs.originResult.features;
                viewer.entities.removeAll();

                for(var i=0;i<selectedFeatures.length;i++){

                    var value = selectedFeatures[i].fieldValues["0"];
                    var feature = selectedFeatures[i];

                    for(var j = 0; j < feature.fieldNames.length ; j++){
                        var index = j.toString();
                        if(j == 0){
                            var des = '<table class="cesium-infoBox-defaultTable"><tbody>' + '<tr><th>' + selectedFeatures[i].fieldNames["0"] + '</th><td>' + selectedFeatures[i].fieldValues["0"] + '</td></tr>';
                        }
                        else if( j == feature.fieldNames.length - 1){
                            des += '<tr><th>' + selectedFeatures[i].fieldNames[index] + '</th><td>' + selectedFeatures[i].fieldValues[index] + '</td></tr>' + "</tbody></table>";
                        }
                        else{
                            des += '<tr><th>' + selectedFeatures[i].fieldNames[index] + '</th><td>' + selectedFeatures[i].fieldValues[index] + '</td></tr>';
                        }
                    }

                    viewer.entities.add({
                        id:i,
                        position :Cesium.Cartesian3.fromDegrees(116.4452364361199+i,39.91946138913283+i,10.2353791933507947),
                        billboard :{
                            image : require('../../../assets/VM/location.png'),
                            width:20,
                            height:30,
                        },
                        show:true,
                        name : selectedFeatures[i].fieldValues["13"],
                        description: des
                    })
                }
            }
        },
        doSqlQuery(viewer,SQL='SMID<5'){
            let _this=this,getFeatureParam, getFeatureBySQLService, getFeatureBySQLParams;
            getFeatureParam = new SuperMap.REST.FilterParameter({
                attributeFilter: SQL
            });
            getFeatureBySQLParams = new SuperMap.REST.GetFeaturesBySQLParameters({
                queryParameter: getFeatureParam,
                datasetNames: ["tunnel:" + "结构框架"]
            });
            getFeatureBySQLService = new SuperMap.REST.GetFeaturesBySQLService(URL_CONFIG.BIM_DATA, {
                eventListeners: {
                    "processCompleted": _this.onQueryComplete(viewer),
                    "processFailed": _this.processFailed
                }
            });
            getFeatureBySQLService.processAsync(getFeatureBySQLParams);
        },
        processFailed(queryEventArgs){
            alert('查询失败！');
        },
        VideoSwitch(){
            let _this=this;

            _this.viewer.entities._entities._array.forEach(entitie=>entitie._show = !entitie._show);

        },
        getVideos(){
            let _this=this;

            _this.axios.get('/videos')
                .then(result=>{
                    let { code, data } = result.data;

                    if(code == 200){
                        _this.allVideos.splice(0); //清空当前数组

                        data.forEach(video=>{_this.allVideos.push({MOID:video.id,latitude:video.latitude,longitude:video.longitude,height:video.height,url:video.url})});
                    }
                })
        },
        addEntityObj(videos){
            let _this=this;

            _this.viewer.entities.removeAll();
            for(var i=0;i<videos.length;i++){

                _this.viewer.entities.add({
                    id:videos[i].MOID,
                    position :Cesium.Cartesian3.fromDegrees(116.4452364361199+i,39.91946138913283+i,10.2353791933507947),
                    billboard :{
                        image : require('../../../assets/VM/location.png'),
                        width:20,
                        height:30,
                    },
                    show:true,
                    // name : selectedFeatures[i].fieldValues["13"],
                    // description: des
                })

            }
        },
        replaceVideoUrl(videoId){
            let vlc=document.getElementById(VLC.VLC_ID)
            console.log('vlc',vlc)
            let [ video ]=this.allVideos.filter(video=>video.MOID == videoId);
            let mrl=video.url;
            vlc.playlist.add(mrl)

            console.log('vlc2',vlc)
        }

    }
  }

</script>



