<template>
    <div>
        <div class="conditions">
            <Row>
                <!-- <Col span="6">
                    <span class="conditionTitle">付款方式：</span>
                    <Select v-model="conditions.payment" @on-change='queryList()' style="width:60%">
                        <Option value=null>所有</Option>
                        <Option v-for="item in payType" :value="item.val" :key="item.id">{{ item.key }}</Option>
                    </Select>
                </Col> -->
                <Col span="6">
                    <span class="conditionTitle">所属公司：</span>
                    <Select v-model="conditions.companyId" @on-change="queryList()" style="width: 60%">
                        <Option value="null">所有</Option>
                        <Option v-for="item in company" :key="item.id" :value="item.id">{{item.name}}</Option>
                    </Select>
                </Col>
                <Col span="6">
                    <span class="conditionTitle">联系人：</span>
                    <Input v-model="conditions.contact" placeholder="请输入联系人姓名" style="width: 60%" @on-keyup="queryList()"></Input>
                </Col>
                <Col span="6">
                    <span class="conditionTitle">开始时间：</span>
                    <DatePicker type="datetime" placeholder="请选择开始时间" style="width: 60%" v-model="conditions.startTime" @on-change="queryList()"></DatePicker>
                </Col>
                <Col span="6">
                    <span class="conditionTitle">结束时间：</span>
                    <DatePicker type="datetime" placeholder="请选择结束时间" style="width: 60%" v-model="conditions.endTime" @on-change="queryList()"></DatePicker>
                </Col>
            </Row>
        </div>
        <div class="list">
            <Button class="add" type="info" @click="goToMoudle({ path:'/UM/tunnelCustomer/add'})"><Icon type="plus-round" color="#fff"></Icon>添加</Button>
            <Row style="margin-top: 40px;">
                <Col span="6" v-for="(item,index) in customerList" :key="index">
                    <div class="infoList">
                        <div class="company">
                            <Icon type="star"></Icon>
                            <span>{{item.company.name}}</span>
                        </div>
                        <div class="contactInfo">
                            <div class="contact">
                                <Icon type="android-person"></Icon>
                                <span>{{item.contact}}</span>
                            </div>
                            <div class="tel">
                                <Icon type="android-call"></Icon>
                                <span>{{item.tel}}</span>
                            </div>
                        </div>
                        <div class="crtTime">
                            <Icon type="android-time"></Icon>
                            <span>{{item.crtTime}}</span>
                        </div>
                        <div class="option">
                            <Button type="primary" size="small" @click="edit(index)">编辑</Button>
                            <Button type="error" size="small" @click="del(index)">删除</Button>
                        </div>
                    </div>
                </Col>
            </Row>
            <div class="listBox">
            </div>
            <Page :total="page.pageTotal" :current="page.pageNum" :page-size="page.pageSize" show-sizer show-total
                    placement="top" @on-change="handlePage" @on-page-size-change='handlePageSize' show-elevator style="text-align: right;margin-top: 20px;"></Page>
        </div>
    </div>
</template>
<script>
import axios from "axios";
import types from '../../../../../static/Enum.json'
export default {
    data(){
        return{
            page: {
                pageNum: 1,
                pageSize: 10,
                pageTotal: 0
            },
            customerList:[],
            conditions:{
                payment: null,
                companyId:null,
                contact: null,
                startTime: null,
                endTime: null
            },
            payType:[],
            company: [],
        }
    },
    computed:{
        params() {
            let param = {
                pageNum: this.page.pageNum,
                pageSize: this.page.pageSize,
                companyId:this.conditions.companyId,
                contact: this.conditions.contact,
                startTime: new Date(this.conditions.startTime).getTime(),
                endTime: new Date(this.conditions.endTime).getTime()
            };
            return Object.assign({}, param);
        }
    },
    mounted(){
        //获取所有的付款方式
        axios.get('/paytype-enums').then(response=>{
            let{ code,data } = response.data
            if(code==200){
                this.payType = data;
            }
        });
        //获取所有的公司
        axios.get('/companies').then(response=>{
            let{ code,data } = response.data
            if(code==200){
                this.company = data;
            }
        })
        this.queryList()
    },
    methods:{
        queryList: function(){
            axios.post('customers/datagrid',(this.params)).then( response => {
                let { code,data } = response.data;
                if(code==200){
                    for( let index in data.list ){
                        data.list[index].crtTime = new Date( data.list[index].crtTime ).format( "yyyy-MM-dd hh:mm:s" )
                    }
                    this.customerList = data.list;
                    this.page.pageTotal = data.total
                }
            })
        },
        goToMoudle: function (path) {
            this.$router.push(path);
        },
        goMoudle: function (index,type) {
            this.$router.push({
                name: '添加管廊客户',
                params: {
                    id: this.customerList[index].id,
                    type: type
                }
            });
        },
        edit(index,type){
            this.goMoudle(index,types.pageType.Edit);
        },
        ok () {
            this.delIt = true;
        },
        del(index) {
            axios.delete("/customers/" + this.customerList[index].id).then(response => {
                this.customerList.splice(this.customerList[index].id, 1);
                this.queryList()
            });
        },
        handlePage(value) {
            this.page.pageNum = value
            this.queryList()
        },
        handlePageSize(value) {
            this.page.pageSize = value
            this.queryList()
        },
    }
}
</script>
<style scoped>
    .infoList{
        border: 1px solid#dddfe1;
        width: 260px;
        margin: 10px auto;
        padding: 5px 0px;
        border-radius: 4px;
        box-shadow: 5px 6px 4px rgba(0, 0, 0, .2);
    }
    /* .infoList:nth-child(2n+1){
        background: #f8f8f9;
    } */
    .company{
        padding-left: 10px;
        font-size: 22px;
    }
    .contact,.tel{
        padding-left: 10px;
        display: inline-block;
        width: 45%;
        line-height: 45px;
    }
    .crtTime,.option{
        display: inline-block;
    }
    .crtTime{
        float: right;
        padding: 0px 10px 0px 0px;
    }
    .option{
        padding: 0px 0px 0px 10px;
    }
    .conditions{
         padding: 10px 20px;
    }
    .list{
        padding: 10px 20px;
        margin-top: 10px;
    }
    .add{
        width: 100px;
        float: right;
    }
    .ivu-icon {
        margin-right: 5px;
        color: #ff9b00;
    }
    .listBox{
        margin-top: 65px;
    }
</style>



